import React from "react";
import { Row, Button } from "reactstrap";
import { IEventListItemViewModel } from "../../../models/EventListItemViewModel";
import EventListItemView from "./event";
import Pagination from "../../../containers/pages/Pagination";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { AddNewEventModal } from "./addevent";
import { EventClient, IEvent, IEventClientListResponse } from "../../../api/eventsClient";
import { connect } from 'react-redux';
import { NotificationManager } from "../../../components/common/react-notifications";
import EditEventModal from "./editevent";
import { ContextMenu, MenuItem } from "react-contextmenu";
import ParallaxItem from "../../../components/parallax/parallaxItem";
import { Redirect } from 'react-router-dom'
import { BASE_URL } from "../../../api/settings";
import { AuthService } from "../../../services/auth.service";

export interface Event {
    id: number,
    img: string,
    status: string,
    statusColor: string,
    date: string
}

export interface IEventDefaultViewState {
    events: IEvent[],
    modalOpen: boolean,
    modalEditOpen: boolean,
    currentPage: number,
    totalPage: number
    editedEvent: any,
    response: IEventClientListResponse | null,
    loading: boolean
}

export class DefaultView extends React.Component<any, IEventDefaultViewState> {
    pageSizes = [4, 8, 12, 20];

    eventClient: EventClient;

    authService: AuthService = new AuthService();

    constructor(props) {
        super(props);
        this.state = {
            currentPage: 1,
            totalPage: 1,
            events: [],
            modalOpen: false,
            modalEditOpen: false,
            editedEvent: null,
            response: null,
            loading: true
        }
        this.eventClient = new EventClient();
    }

    componentDidMount() {
        this.populateData();
    }

    populateData = () => {
        this.setState({ loading: true })
        this.eventClient.List(this.authService.getId()).then((response) => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
            } else {
                if (response.data.Data != null) {
                    this.setState({
                        currentPage: response.data.Data.Page,
                        totalPage: response.data.Data.PageCount,
                        events: response.data.Data.EventsList || [],
                        response: response.data.Data
                    });
                }
            }
            this.setState({ loading: false })
        });
    }

    get events() {
        let ev = [...this.state.events];
        ev.reverse();
        return ev;
    }

    setModalOpen = (modal: "Add" | "Edit", modalOpen: boolean) => {
        if (modal == "Add") {
            this.setState({ modalOpen: modalOpen });
        } else {
            this.setState({ modalEditOpen: modalOpen });
        }
    }

    deleteEvent = (event: IEvent, index) => {
        NotificationManager.info("Deleting event...", "", 3000, null, null, '');
        this.eventClient.Delete(event.Id).then((response) => {
            if (response.data) {
                // let d = [...this.state.events];
                // d.splice(index, 1);
                // this.setState({
                //     events: d
                // });
                this.populateData();
                NotificationManager.success("", 'Event deleted', 3000, null, null, '');


            }
        });
    }

    editEvent = (event: IEvent) => {
        this.eventClient.GetAddEventDialog(event.Id).then((response) => {
            if (response.data.Error) {
                NotificationManager.error("Cannot get data", 'Error', 3000, null, null, '');
                return;
            }

            console.log('response edit', response.data)

            this.setState({ editedEvent: { ...response.data.Data, Id: event.Id } }, () => {
                this.setState({ modalEditOpen: true });
            })
        });
    }

    onContextMenuClick = (e, data) => {
        if (data.action == "settings") {
            this.editEvent(data.event);
        } else if (data.action == "delete") {
            var indexOf = this.state.events.indexOf(data.event);
            this.deleteEvent(data.event, indexOf);
        }
    };

    onContextMenu = (e, data) => {
        return true;
    };

    get parallaxData() {
        if (this.state.response == null) {
            return null;
        }

        return {
            CoverPhoto: this.state.response.CoverPhotoUrl,
            Top: this.state.response.CoverOffset ? this.state.response.CoverOffset : 0,
            ObjectId: Number(this.state.response.PhotographerId),
            Mode: "photographer",
            CanEdit: true
        }
    }

    render() {
        if (!this.props.user) {
            return (<Redirect to={`/redirect?url=${`${BASE_URL}/Event/List/${(this.props as any).match.params.photographerId}`}`} />)
        }

        if (!this.props.match.params.photographerId) {

            return (<Redirect to={`/photo-hosting/${this.authService.getId()}`} />)
        };
        return (<>
            {this.state.loading && <div className="loading" />}
            {!this.state.loading &&
                <>
                    <Row>
                        <Colxx xxs="12">
                            <div className="mb-2">
                                <div className="text-zero top-right-button-container">
                                    <Button
                                        color="primary"
                                        size="lg"
                                        className="top-right-button"
                                        onClick={() => this.setModalOpen("Add", true)}
                                    >
                                        <i className="simple-icon-plus" /> ADD NEW EVENT
                                    </Button>
                                    {'  '}

                                </div>
                                <Breadcrumb heading="Events" match={this.props.match} />
                            </div>
                            <Separator className="mb-5" />
                        </Colxx>
                    </Row>
                    <Row className="mb-3">
                        <Colxx xxs="12">
                            {this.parallaxData != null && <ParallaxItem canEdit={this.parallaxData.CanEdit} coverPhoto={this.parallaxData.CoverPhoto} top={this.parallaxData.Top} mode={this.parallaxData.Mode} objectId={this.parallaxData.ObjectId} />}
                        </Colxx>
                    </Row>
                    <div className="disable-text-selection">
                        <Row>
                            {this.events?.map((event, index) => {
                                return (<EventListItemView key={`${event.Id}-${index}`} event={event} onDelete={(ev) => this.deleteEvent(ev, index)} onEdit={(ev) => { this.editEvent(ev) }} />)
                            })}
                            <Pagination
                                currentPage={this.state.currentPage}
                                totalPage={this.state.totalPage}
                                onChangePage={(i) => { }}
                            />
                        </Row>
                    </div>
                    <ContextMenu id="menu_id" onShow={(e) => this.onContextMenu(e, e.detail.data)}>
                        <MenuItem onClick={this.onContextMenuClick} data={{ action: 'settings' }}>
                            <i className="simple-icon-docs" /> <span>Settings</span>
                        </MenuItem>

                        <MenuItem onClick={this.onContextMenuClick} data={{ action: 'delete' }}>
                            <i className="simple-icon-trash" /> <span>Delete</span>
                        </MenuItem>
                    </ContextMenu>
                    <AddNewEventModal modalOpen={this.state.modalOpen} toggleModal={() => this.setModalOpen("Add", !this.state.modalOpen)} onCreate={() => this.populateData()} />
                    <EditEventModal event={this.state.editedEvent} modalOpen={this.state.modalEditOpen} toggleModal={() => this.setModalOpen("Edit", !this.state.modalEditOpen)} onEdited={() => this.populateData()} />
                </>}
        </>)
    }
}

const mapStateToProps = ({ authUser }) => {
    return authUser;
};

export default connect(mapStateToProps)(DefaultView);
