import Axios, { AxiosRequestConfig } from "axios";
import * as globalSettings from "./settings";
import { IApiResponse } from "./IApiResponse";
import { layer } from "@fortawesome/fontawesome-svg-core";
import { ICategory } from "../views/photoOrder/categories/category";
import { IAttributeGroup, ICreateAttributeGroup } from "../views/photoOrder/attributes/attribute";

export class AttributesClient {
    settings: AxiosRequestConfig = {};

    constructor() {
        let settings = { ...globalSettings.CONFIG }
        settings.withCredentials = false;
        if (window.localStorage.getItem("loginData")) {
            let data = JSON.parse(window.localStorage.getItem("loginData") as string);

            settings.headers["Authorization"] = data.Token;
            this.settings = settings;
        }
    }

    List(key: string) {
        var config = {
            ...this.settings,
        }
        return Axios.get<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/Attributes/GetAttributesGroup?key=${key}`, this.settings);
    }

    Add(attributeGroup: ICreateAttributeGroup, key: string) {
        return Axios.post<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/Attributes/CreateNewAttributesGroup`, attributeGroup, this.settings);
    }

    Delete(attributeGroupId: number) {
        return Axios.delete<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/attributes/DeleteAttributeGroup?id=${attributeGroupId}`, this.settings);

    }

    Update(attributeGroup: ICreateAttributeGroup, key: string) {
        return Axios.put<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/attributes/UpdateAttributeGroup`, attributeGroup, this.settings);
    }

    Positions(positions: { id: number, position: number }[]) {
        return Axios.put<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/attributes/UpdateAttributesGroupPosition`, positions, this.settings);
    }
}