import React from 'react';
import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Input,
    Label,
    FormGroup,
    InputGroup,
    CustomInput,
    InputGroupAddon,
    InputGroupText,
    FormText,
} from 'reactstrap';
import IntlMessages from '../../../helpers/IntlMessages';
import { Formik, Form, Field } from 'formik';
import { PhotoClient } from '../../../api/photoClient';
import { NotificationManager } from '../../../components/common/react-notifications';
import { IAttributeGroup, IAttribute, ICreateAttribute } from './attribute';
import { FileToBase64 } from '../../../helpers/FileHelper';

export interface IAddEditAttributeModalProps {
    modalOpen: boolean,
    toggleModal: () => void,
    onSave: (attribute: ICreateAttribute) => void,
    attribute: IAttribute | null
}

export class AddEditAttributeModal extends React.Component<IAddEditAttributeModalProps, any> {
    _initialValues = {
        Name: "",
        Id: 0,
        AttribiuteGroupId: 0,
        Descriptipn: "",
        CorrectionPrice: 0,
        CorrectionPercent: 0,
        CorrectionType: 0
    }

    constructor(props) {
        super(props);
        this.state = {
            customSize: false
        }
    }

    get initialValues() {
        if (this.props.attribute) {
            return {
                Name: this.props.attribute.Name,
                Id: this.props.attribute.Id,
                Description: this.props.attribute.Description,
                CorrectionPrice: this.props.attribute.PriceCorrectionCurrent,
                CorrectionPercent: this.props.attribute.PriceCorrectionPercent,
                Position: this.props.attribute.Position,
                AttributeGroupId: this.props.attribute.AttributeGroupId,
                CorrectionType: this.props.attribute.PriceCorrectionPercent > 0 ? 1 : 0
            }
        }
        return this._initialValues;
    }

    onSubmit = async (values, formik) => {
        let fileName = "";
        let fileBase64 = "";
        if (values.File) {
            fileName = values.File.name;
            fileBase64 = await FileToBase64(values.File);
        }
        if (values.CorrectionType == 0) {
            values.CorrectionPercent = 0;
        } else {
            values.CorrectionPrice = 0;
        }

        this.props.onSave({ Id: values.Id, Name: values.Name, Description: values.Description, PriceCorrectionPercent: values.CorrectionPercent, PriceCorrectionCurrent: values.CorrectionPrice, Position: values.Position, AttributeGroupId: values.AttributeGroupId });
        this.props.toggleModal();
        formik.setSubmitting(false);
    }

    render() {
        return (<Formik
            initialValues={this.initialValues}
            onSubmit={this.onSubmit}
            enableReinitialize={true}
        >
            {({
                handleSubmit,
                setFieldValue,
                setFieldTouched,
                handleChange,
                handleBlur,
                values,
                errors,
                touched,
                isSubmitting,
                submitForm
            }) => (
                <Form><Modal
                    isOpen={this.props.modalOpen}
                    toggle={this.props.toggleModal}
                    wrapClassName="modal-right"
                    backdrop="static"
                >
                    <ModalHeader toggle={this.props.toggleModal}>
                        {this.props.attribute?.Id == 0 ? "Add attribute" : "Edit attribute"}
                    </ModalHeader>
                    <ModalBody>
                        <FormGroup>
                            <Label for="Name">
                                Name
                                </Label>
                            <Field className="form-control" name="Name" type="text" id="Name" placeholder={"Name"} maxLength={20} />
                            <FormText color="muted">
                                Maximum 20 letters
                                </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label for="Description">
                                Description
                                </Label>
                            <Field className="form-control" name="Description" type="text" id="Description" placeholder={"Description"} maxLength={100} />
                            <FormText color="muted">
                                Maximum 100 letters
                                </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label for="CorrectionType">
                                Price correction type
                                </Label>
                            <select
                                id="CorrectionType"
                                name="CorrectionType"
                                className="form-control"
                                value={values.CorrectionType}
                                onChange={handleChange}
                                onBlur={handleBlur}
                            >
                                <option value={1}>Percentage (%)</option>
                                <option value={0}>Currency ()</option>
                            </select>
                        </FormGroup>
                        {values.CorrectionType == 0 &&
                            <FormGroup>
                                <Label for="Price">
                                    Price correction [&euro;]
                                </Label>
                                <InputGroup className="mb-3">
                                    <Field className="form-control" name="CorrectionPrice" type="number" id="CorrectionPrice" placeholder={"Correction Price"} />
                                    <InputGroupAddon addonType="append">
                                        <InputGroupText>&euro;</InputGroupText>
                                    </InputGroupAddon>
                                </InputGroup>
                            </FormGroup>
                        }
                        {values.CorrectionType == 1 &&
                            <FormGroup>
                                <Label for="Price">
                                    Percentage correction [%]
                                </Label>
                                <InputGroup className="mb-3">
                                    <Field className="form-control" name="CorrectionPercent" type="number" id="CorrectionPercent" placeholder={"Correction Percent"} />
                                    <InputGroupAddon addonType="append">
                                        <InputGroupText>%</InputGroupText>
                                    </InputGroupAddon>
                                </InputGroup>
                            </FormGroup>
                        }

                    </ModalBody>
                    <ModalFooter>
                        <Button color="secondary" outline onClick={this.props.toggleModal}>
                            Cancel
                            </Button>
                        <Button color="primary" type="submit" onClick={submitForm}>
                            Save
                            </Button>
                    </ModalFooter>
                </Modal>
                </Form>)
            }
        </Formik>
        );
    }
}

export default AddEditAttributeModal;
