import React from "react";
import { Modal, ModalHeader, ModalFooter, Button, ModalBody } from "reactstrap";

export interface IDeleteModalProps {
    isOpen: boolean
    onDelete: () => void
    toggle: (show: boolean) => void
    header: string,
    body: string
}

export class DeleteModal extends React.Component<IDeleteModalProps, any> {
    render() {
        return (
            <Modal
                isOpen={this.props.isOpen}
                toggle={() => this.props.toggle(!this.props.isOpen)}
            >
                <ModalHeader>
                    {this.props.header}
                </ModalHeader>
                <ModalBody>
                    {this.props.body}
                </ModalBody>
                <ModalFooter style={{ display: "flex", justifyContent: "space-between" }}>
                    <Button
                        color="secondary"
                        onClick={() => this.props.toggle(false)}
                    >
                        Cancel
                    </Button>
                    <Button
                        color="danger"
                        onClick={() => { this.props.onDelete(); this.props.toggle(false) }}
                    >
                        YES
                    </Button>{' '}

                </ModalFooter>
            </Modal>
        );
    }
}