import React from "react";
import { Colxx } from "../../../components/common/CustomBootstrap";
import { ContextMenuTrigger } from "react-contextmenu";
import { NavLink, Card, UncontrolledDropdown, DropdownToggle, DropdownItem, DropdownMenu, Row, Collapse } from "reactstrap";
import { IAttributeGroup, IAttribute } from "./attribute";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faBars } from "@fortawesome/free-solid-svg-icons"
import { DraggableProvidedDragHandleProps, Droppable, Draggable } from "react-beautiful-dnd";
import classnames from "classnames";
import shortid from "shortid";

interface ICategoryListItem {
    attributeGroup: IAttributeGroup;
    onCheckItem: (e, id) => void;
    onDelete: (attributeGroup: IAttributeGroup) => void;
    onEdit: (attributeGroup: IAttributeGroup) => void;
    onAddAttribute: (attributeGroup: IAttributeGroup) => void;
    onDeleteAttribute: (attribute: IAttribute) => void;
    onEditAttribute: (attribute: IAttribute) => void;
    groupDragHandler: DraggableProvidedDragHandleProps | undefined;
}



export class AttributeListItem extends React.Component<ICategoryListItem, any> {

    constructor(props) {
        super(props);
        this.state = {
            isCollapsed: false,
            isAttrImage: {

            }
        }
    }

    isImage(attrId: number) {
        return this.state.isAttrImage[attrId] == null || this.state.isAttrImage[attrId] == true;
    }

    onImageError = (attrId: number) => {
        console.log('on image-error');
        let images = { ... this.state.isAttrImage }
        images[attrId] = false
        this.setState({ isAttrImage: images });
    }

    get orderedAttributes() {
        let attributes: IAttribute[] = this.props.attributeGroup.attributes;
        return attributes.sort((a, b) => a.Position - b.Position);
    }

    render() {
        return (
            <Colxx xxs="12" key={"attrgroup-" + this.props.attributeGroup.Id} className="mb-3 productList">
                <ContextMenuTrigger id="product_menu" collect={(props) => { return { data: props.data, product: this.props.attributeGroup } }}>
                    <Card  {...this.props.groupDragHandler} onClick={(event) => this.props.onCheckItem(event, this.props.attributeGroup.Id)} className={"d-flex flex-row productList__card"}>
                        <div className="pl-2 d-flex flex-grow-1 min-width-zero">
                            <div className="card-body align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero align-items-lg-center">
                                <NavLink to={`?p=1`} className="w-70">
                                    <p style={{ cursor: "pointer" }} className="list-item-heading mb-1 truncate d-flex align-items-center">
                                        {this.props.attributeGroup.Name}
                                        <a className="ml-3" style={{ width: "20px", cursor: "grab", display: "inline-block" }} >
                                            {this.props.attributeGroup.attributes?.length > 0 && <div onClick={() => this.setState({ isCollapsed: !this.state.isCollapsed })} style={{ cursor: "pointer" }} className={classnames("glyph-icon", { "simple-icon-arrow-left": this.state.isCollapsed, 'simple-icon-arrow-down': !this.state.isCollapsed })} />}
                                        </a>
                                    </p>
                                </NavLink>
                                <div className="mb-1 w-30 text-right pr-3">
                                    <UncontrolledDropdown className="productList__dropdown">
                                        <DropdownToggle caret color="secondary" outline>
                                            Actions
                                        </DropdownToggle>
                                        <DropdownMenu>
                                            <DropdownItem onClick={() => this.props.onAddAttribute(this.props.attributeGroup)}>
                                                <i className="simple-icon-docs" /> <span>Add attribute</span>
                                            </DropdownItem>
                                            <DropdownItem onClick={() => this.props.onEdit(this.props.attributeGroup)}>
                                                <i className="simple-icon-docs" /> <span>Edit</span>
                                            </DropdownItem>
                                            <DropdownItem onClick={() => this.props.onDelete(this.props.attributeGroup)}>
                                                <i className="simple-icon-trash" /> <span>Delete</span>
                                            </DropdownItem>
                                        </DropdownMenu>
                                    </UncontrolledDropdown>
                                </div>
                                <div>
                                    <FontAwesomeIcon icon={faBars} />
                                </div>
                            </div>
                        </div>
                    </Card>
                    <Collapse isOpen={this.state.isCollapsed}>
                        <Row>
                            <Colxx className={"pl-3"}>
                                <Droppable droppableId={this.props.attributeGroup.Id.toString()} type={"attributeGroup-" + this.props.attributeGroup.Id}>
                                    {(provided, snapshot) => (
                                        <div ref={provided.innerRef}>
                                            {this.orderedAttributes.map((attribute, index) => {
                                                return (
                                                    <Draggable key={'attributeDragable-' + attribute.Id} draggableId={attribute.Id.toString()} index={index}>
                                                        {(provided, snapshot) => (
                                                            <div ref={provided.innerRef}  {...provided.draggableProps}>
                                                                <Card {...provided.dragHandleProps} key={'attribute-' + attribute.Id} onClick={(event) => this.props.onCheckItem(event, this.props.attributeGroup.Id)} className={"d-flex flex-row productList__card mt-1"} style={{ marginLeft: "3rem" }}>
                                                                    <div className="pl-2 d-flex flex-grow-1 min-width-zero">
                                                                        <div className="card-body align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero align-items-lg-center">
                                                                            <NavLink to={`?p=1`} className="w-20">
                                                                                <p className="list-item-heading mb-1 truncate">
                                                                                    {attribute.Name}
                                                                                </p>
                                                                            </NavLink>
                                                                            <p className={classnames("mb-1 text-muted text-small w-40")}>{attribute.Description}</p>
                                                                            <div className="w-30">
                                                                                Price correction:
                                                                                <strong className="ml-1">
                                                                                    {attribute.PriceCorrectionCurrent > 0 ? attribute.PriceCorrectionCurrent.toFixed(2) + "" : ""}
                                                                                    {attribute.PriceCorrectionPercent > 0 ? attribute.PriceCorrectionPercent.toFixed(2) + "%" : ""}
                                                                                    {attribute.PriceCorrectionPercent == 0 && attribute.PriceCorrectionCurrent == 0 ? "No price correction" : ""}
                                                                                </strong>

                                                                            </div>
                                                                            {/* {attribute.PriceCorrectionCurrent > 0 && <p className="mb-1 text-muted text-small w-20 w-sm-100">{attribute.PriceCorrectionCurrent} &euro;</p>}
                                                                            {attribute.PriceCorrectionPercent > 0 && <p className="mb-1 text-muted text-small w-20 w-sm-100">{attribute.PriceCorrectionPercent}%</p>} */}
                                                                            <div className="mb-1 w-10 text-right  pr-3">
                                                                                <UncontrolledDropdown className="productList__dropdown">
                                                                                    <DropdownToggle caret color="secondary" outline>
                                                                                        Actions
                                                                                </DropdownToggle>
                                                                                    <DropdownMenu>
                                                                                        <DropdownItem onClick={() => this.props.onEditAttribute(attribute)}>
                                                                                            <i className="simple-icon-docs" /> <span>Edit</span>
                                                                                        </DropdownItem>
                                                                                        <DropdownItem onClick={() => this.props.onDeleteAttribute(attribute)}>
                                                                                            <i className="simple-icon-trash" /> <span>Delete</span>
                                                                                        </DropdownItem>
                                                                                    </DropdownMenu>
                                                                                </UncontrolledDropdown>
                                                                            </div>
                                                                            <div>
                                                                                <FontAwesomeIcon icon={faBars} />
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </Card>
                                                            </div>
                                                        )}
                                                    </Draggable>
                                                )
                                            })
                                            }
                                            {provided.placeholder}
                                        </div>
                                    )}
                                </Droppable>
                            </Colxx>
                        </Row>
                    </Collapse>

                </ContextMenuTrigger>
            </Colxx>
        );
    }
}

export default AttributeListItem;