import { ICategory } from "./categories/category";
import { IAttribute, IAttributeGroup } from "./attributes/attribute";

export const responseItemToCategory = (item: any): ICategory => {
    var category: ICategory = {
        Name: item.name,
        Id: item.id,
        Description: item.description,
        FilePath: item.imageUrl,
        Position: item.position,
        IsHidden: item.isHidden,
        ProductsPositions: item.ProductsPositions
    }
    return category;
}

export const responseAttributeItemToAttribute = (item: any, attributeGroupId: number): IAttribute => {
    return {
        Id: item.id,
        Name: item.name,
        Description: item.description,
        PriceCorrectionPercent: item.priceCorrectionPercent,
        PriceCorrectionCurrent: item.priceCorrectionCurrent,
        Position: item.position,
        AttributeGroupId: attributeGroupId
    }
}

export const responseItemToAttributeGroup = (item: any): IAttributeGroup => {
    let i = {
        Id: item.id,
        Name: item.name,
        Key: item.key,
        attributes: [],
        Position: item.position
    };

    (item.attributes as any[]).forEach(attr => {
        (i.attributes as any[]).push(responseAttributeItemToAttribute(attr, i.Id));
    });

    return i;
}
