import React from "react";
import { Button, Col, CustomInput, InputGroup, InputGroupAddon, InputGroupText, Row } from "reactstrap";
import { Colxx } from "../../../../components/common/CustomBootstrap";
import * as _ from "underscore";

export interface ITemplateRange {
    positionX: number,
    positionY: number,
    sizeX: number,
    sizeY: number
}

export interface ITemplateRangeProps {
    //priceRange: IPriceRange[]
    onChange: (data: { templates: ITemplateRange[] }) => void;
}

export interface IPriceRangeSelectorState {
    templates: ITemplateRange[],
    form: {
        positionX?: number,
        positionY?: number,
        sizeX?: number,
        sizeY?: number
    }
}

export class TemplateSelector extends React.Component<ITemplateRangeProps, IPriceRangeSelectorState> {

    constructor(props) {
        super(props);
        this.state = {
            templates: [],
            form: {
                positionX: 0,
                positionY: 0,
                sizeX: 0,
                sizeY: 0
            }
        }
    }

    onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        let state = { ...this.state }
        if (e.target.value) {
            state.form[e.target.name] = e.target.valueAsNumber
        } else {
            state.form[e.target.name] = null;
        }

        this.setState(state);
    }

    isButtonDisabled = () => {
        return false;
        //return !this.state.form.positionX || !this.state.form.positionY || !this.state.form.sizeX || !this.state.form.sizeY;
    }

    onAddWindow = () => {
        var item = { ...this.state.form };
        var data = [...this.state.templates];
        data.push((item as any));
        this.setState({ templates: data }, () => {
            this.props.onChange((data as any));
            this.clear();

        })

    }


    clear = () => {
        console.log('clear');
        let state = {
            ...this.state,
            form: {
                positionX: 0,
                positionY: 0,
                sizeX: 0,
                sizeY: 0
            }
        }
        this.setState(state);
    }

    onRemoveLast = () => {
        let list = [...this.state.templates];
        list.pop();
        this.setState({ templates: list }, () => {
            this.props.onChange((list as any));
        })
    }

    render() {
        return (
            <>
                <Row className="mt-2 mb-2">
                    <Colxx xxs={12}>
                        {
                            this.state.templates.map((template, index) => (
                                <Row key={`template-${index}`}>
                                    <Colxx xxs={2} className="pr-0">
                                        <div style={{ width: "100%", height: "100%", display: "flex", alignItems: "center" }}>Window {index + 1}</div>
                                    </Colxx>
                                    <Colxx xxs={5} className="pr-0">
                                        <div style={{ width: "100%", height: "100%", display: "flex", alignItems: "center" }}>Position&nbsp;<strong>{template.positionX}px {template.positionY}px</strong></div>
                                    </Colxx>
                                    <Colxx xxs={5} className="pr-0">
                                        <div style={{ width: "100%", height: "100%", display: "flex", alignItems: "center" }}>Size&nbsp;<strong>{template.sizeX}px x {template.sizeY}px</strong></div>
                                    </Colxx></Row>
                            ))
                        }


                    </Colxx>

                </Row>
                <Row style={{ marginTop: "3rem" }}>
                    <Colxx xxs={2} className="pr-0">
                        <div style={{ width: "100%", height: "100%", display: "flex", alignItems: "center" }}>Position in px</div>

                    </Colxx>
                    <Colxx xxs={5} className="pr-0">
                        <InputGroup>
                            <input onChange={(e) => this.onChange(e)} name="positionX" value={this.state.form.positionX} type="number" placeholder="X" className="form-control" />
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText>x</InputGroupText>
                            </InputGroupAddon>
                        </InputGroup>
                    </Colxx>
                    <Colxx xxs={5} className="pl-0">
                        <InputGroup>
                            <input onChange={(e) => this.onChange(e)} name="positionY" value={this.state.form.positionY} type="number" placeholder="Y" className="form-control" />
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText>y</InputGroupText>
                            </InputGroupAddon>
                        </InputGroup>
                    </Colxx>
                    <Colxx xxs={2} className="pr-0">
                        <div style={{ width: "100%", height: "100%", display: "flex", alignItems: "center" }}>Size in px</div>

                    </Colxx>
                    <Colxx xxs={5} className="pr-0">
                        <InputGroup>
                            <input onChange={(e) => this.onChange(e)} name="sizeX" value={this.state.form.sizeX} type="number" placeholder="X" className="form-control" />
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText>x</InputGroupText>
                            </InputGroupAddon>
                        </InputGroup>
                    </Colxx>
                    <Colxx xxs={5} className="pl-0">
                        <InputGroup>
                            <input onChange={(e) => this.onChange(e)} name="sizeY" value={this.state.form.sizeY} type="number" placeholder="Y" className="form-control" />
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText>y</InputGroupText>
                            </InputGroupAddon>
                        </InputGroup>
                    </Colxx>

                    <Colxx xxs={6} className="mt-1">
                        <Button disabled={this.isButtonDisabled()} color="primary" outline onClick={() => this.onAddWindow()}><i className="simple-icon-plus" /> Add new window</Button>
                    </Colxx>
                    <Colxx xxs={6} className="mt-1 text-right">
                        <Button color="danger" outline onClick={() => this.onRemoveLast()}><i className="simple-icon-trash" /> Remove</Button>
                    </Colxx>
                </Row>
            </>
        )
    }
}