import React from 'react';
import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Input,
    Label,
    FormGroup,
    InputGroup,
    CustomInput,
    InputGroupAddon,
    InputGroupText,
    FormText,
} from 'reactstrap';
import IntlMessages from '../../../helpers/IntlMessages';
import { Formik, Form, Field } from 'formik';
import { PhotoClient } from '../../../api/photoClient';
import { NotificationManager } from '../../../components/common/react-notifications';
import { ICategory, ISaveCategory } from './category';
import { FileToBase64 } from '../../../helpers/FileHelper';

export interface IAddCategoryModalProps {
    modalOpen: boolean,
    toggleModal: () => void,
    onSave: (category: ISaveCategory) => void,
    category: ICategory | null,
    dataKey: string
}

export class AddEditCategoryModal extends React.Component<IAddCategoryModalProps, any> {
    _initialValues = {
        Name: "",
        Description: "",
        File: null,
        Id: 0,
        IsHidden: false
    }

    constructor(props) {
        super(props);
        this.state = {
            customSize: false
        }
    }

    get initialValues() {
        if (this.props.category) {
            return {
                Name: this.props.category.Name,
                Description: this.props.category.Description,
                Id: this.props.category.Id,
                File: null,
                IsHidden: this.props.category.IsHidden
            }
        }
        return this._initialValues;
    }

    onSubmit = async (values, formik) => {
        let fileName = "";
        let fileBase64 = "";
        if (values.File) {
            fileName = values.File.name;
            fileBase64 = await FileToBase64(values.File);
        }
        this.props.onSave({ Id: values.Id, Description: values.Description || "", Name: values.Name || "", FileAsBase64: fileBase64, FileName: fileName, Key: this.props.dataKey, IsHidden: values.IsHidden });
        this.props.toggleModal();
        formik.setSubmitting(false);
    }

    render() {
        return (<Formik
            initialValues={this.initialValues}
            onSubmit={this.onSubmit}
            enableReinitialize={true}
        >
            {({
                handleSubmit,
                setFieldValue,
                setFieldTouched,
                handleChange,
                handleBlur,
                values,
                errors,
                touched,
                isSubmitting,
                submitForm
            }) => (
                <Form><Modal
                    isOpen={this.props.modalOpen}
                    toggle={this.props.toggleModal}
                    wrapClassName="modal-right"
                    backdrop="static"
                >
                    <ModalHeader toggle={this.props.toggleModal}>
                        {this.props.category?.Id == 0 ? "Add category" : "Edit category"}
                    </ModalHeader>
                    <ModalBody>

                        <FormGroup>
                            <Label for="Name">
                                Name
                                </Label>
                            <Field className="form-control" name="Name" type="text" id="Name" placeholder={"Name"} maxLength={20} />
                            <FormText color="muted">
                                Maximum 20 letters
                                </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label for="Description">
                                Description
                                </Label>
                            <Field className="form-control" name="Description" type="text" id="Description" placeholder={"Description"} maxLength={100} />
                            <FormText color="muted">
                                Maximum 100 letters
                                </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label for="File">
                                Image
                                </Label>
                            <InputGroup>
                                <CustomInput
                                    type="file"
                                    id="File"
                                    name="File"

                                    onChange={(event) => {
                                        if (event != null && event.currentTarget != null && event.currentTarget.files) {
                                            setFieldValue("File", event.currentTarget.files[0]);
                                        }
                                    }} />
                            </InputGroup>
                            <FormText color="muted">
                                Maximum 800x800 pixels
                                </FormText>
                        </FormGroup>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="secondary" outline onClick={this.props.toggleModal}>
                            Cancel
                            </Button>
                        <Button color="primary" type="submit" onClick={submitForm}>
                            Save
                            </Button>
                    </ModalFooter>
                </Modal>
                </Form>)
            }
        </Formik>
        );
    }
}

export default AddEditCategoryModal;
