import React from 'react';
import { injectIntl } from 'react-intl';
import { Row, Card, CardBody } from 'reactstrap';
import { Colxx, Separator } from '../../components/common/CustomBootstrap';
import Breadcrumb from '../../containers/navs/Breadcrumb';
import GlideComponent from '../../components/carousel/GlideComponent';
import IconCard from '../../components/cards/IconCard';
import { HomeClient } from '../../api/homeClient';
import NotificationManager from '../../components/common/react-notifications/NotificationManager';

export class DefaultDashboard extends React.Component<any, any> {

  homeClient: HomeClient = new HomeClient();

  state = {
    data: [
      { title: 'You can use photo order for', icon: 'iconsminds-clock', value: 0 },
      { title: 'You can use photo hosting for', icon: 'iconsminds-clock', value: 0 },
    ]
  }

  constructor(props: any) {
    super(props);

    this.homeClient.PhotographerDetails().then(response => {
      if (response.data.Error) {
        NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
      } else {
        let data = [...this.state.data];
        data[0].value = response.data.Data.AddEditPhotographerPopUpViewModel.EventSubscription;
        data[1].value = response.data.Data.AddEditPhotographerPopUpViewModel.MobileSubscription;;
        this.setState({
          data: data
        });
      }
    });
  }

  render() {
    return (
      <>
        <Row>
          <Colxx xxs="12">
            <Card className="mb-4">
              <CardBody>
                <Row>
                  <Colxx sm="6">
                    <img className="img-fluid border-radius" src="/assets/img/dashboard.jpg" />

                  </Colxx>
                  <Colxx sm="6">
                    <p className="font-weight-bold">
                      Welcome to our portal. We hope that you will find here everything you need to manage a modern photo studio.
                          </p>
                    <p>
                      Thanks to our service, you can speed up and automate ordering wedding photos. By adding a link to the gallery, your website becomes a fully interactive place where your clients can easily and pleasantly select photos from the ceremony from any device.
                    <br />Our new plug-in for the Light room software streamlines the work of any photographer.
                      <br />Gain time and customer recognition.
                      <br />For more information, please visit the "Photo Hosting" tab
                    </p>
                    <p>
                      If you copy amateur photos, then you can't do without the Photo Order system.
                      <br />All your customers can now send photos directly from their phone wherever they are.
                      <br />If you have a website, add our link and it will become a fully functioning e-shop within a second.
                      <br />Do not wait, be online 24/7 !!!
                      <br />For more information visit the "Photo Order" tab
                    </p>
                    <p>
                      We constantly strive to ensure that our products meets all your requirements.
                      <br />This is very important to us, so if you have any comments please share with us and we will certainly take them into account in our next update.
                      </p>
                    <p className="font-weight-bold">
                      Black Point Team
                          </p>
                  </Colxx>
                </Row>
              </CardBody>
            </Card>
          </Colxx>
        </Row>
        <Row>
          <Colxx lg="6" xl="6">
            <div className='icon-cards-row'>
              <div key={`icon_card_0`}>
                <IconCard {...this.state.data[0]} className="mb-4" />
              </div>
            </div>
          </Colxx>
          <Colxx lg="6" xl="6">
            <div className='icon-cards-row'>
              <div key={`icon_card_1`}>
                <IconCard {...this.state.data[1]} className="mb-4" />
              </div>
            </div>
          </Colxx>
        </Row>
      </>);
  }
}

export default injectIntl(DefaultDashboard);
