import React from "react";
import { Modal, ModalHeader, ModalBody, ModalFooter, Button } from "reactstrap";
import IntlMessages from "../../../helpers/IntlMessages";
import { IProduct } from "./product";

export interface IDeleteProductModalProps {
    isOpen: boolean
    onDelete: () => void
    toggle: (show: boolean) => void
    title: string,
    description: string
}

export class DeleteProductModal extends React.Component<IDeleteProductModalProps, any> {
    render() {
        return (
            <Modal
                isOpen={this.props.isOpen}
                toggle={() => this.props.toggle(!this.props.isOpen)}
            >
                <ModalHeader>
                    {this.props.title}
                </ModalHeader>
                <ModalBody>
                    {this.props.description}
                </ModalBody>
                <ModalFooter style={{ display: "flex", justifyContent: "space-between" }}>
                    <Button
                        color="secondary"
                        onClick={() => this.props.toggle(false)}
                    >
                        Cancel
                    </Button>
                    <Button
                        color="danger"
                        onClick={() => { this.props.onDelete(); this.props.toggle(false) }}
                    >
                        YES
                    </Button>{' '}

                </ModalFooter>
            </Modal>
        );
    }
}