import React from "react";
import { Row, Button } from "reactstrap";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb"
import CategoryListItem from "./categorylistitem";
import { ICategory, ISaveCategory } from "./category";
import AddEditCategoryModal from "./categoryAddEditModal";
import { DeleteModal } from "../../../components/modals/delete/DeleteModal";
import { DragDropContext, Draggable, Droppable, DropResult } from "react-beautiful-dnd";
import { CategoryClient } from "../../../api/categoriesClient";
import NotificationManager from "../../../components/common/react-notifications/NotificationManager";
import shortid from "shortid";

export interface ICategoriesState {
    categories: ICategory[],
    editedCategory: ICategory | null,
    deletingCategory: ICategory | null
    loading: boolean
}

const reorder = (list: ICategory[], startIndex: number, endIndex: number): ICategory[] => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
};

export class Categories extends React.Component<any, ICategoriesState> {

    // private static Key: string = "ProductCategories"

    categoryClient: CategoryClient

    constructor(props) {
        super(props);

        this.categoryClient = new CategoryClient();

        this.state = {
            categories: [],
            editedCategory: null,
            deletingCategory: null,
            loading: true
        }
    }

    componentDidUpdate(prevProps) {
        if (this.props.dataKey != prevProps.dataKey) {
            this.setState({
                categories: [],
                editedCategory: null,
                deletingCategory: null
            }, () => {
                this.componentDidMount();
            });

        }
    }

    componentDidMount() {
        this.setState({ loading: true });
        this.categoryClient.List(this.props.dataKey).then(response => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'List categories error', 3000, null, null, '');
                return;
            }
            var categories: ICategory[] = [];
            response.data.Data.forEach(item => {
                categories.push(this.responseItemToCategory(item));
            });
            this.setState({ categories: categories, loading: false });
        });
    }

    responseItemToCategory = (item: any): ICategory => {
        var category: ICategory = {
            Name: item.name,
            Id: item.id,
            Description: item.description,
            FilePath: item.imageUrl,
            Position: item.position,
            IsHidden: item.isHidden,
            ProductsPositions: item.ProductsPositions
        }
        return category;
    }

    edit = (category: ISaveCategory) => {
        let categories = [...this.state.categories];
        let c = categories.find(x => x.Id == category.Id);
        c = Object.assign(c, category);
        NotificationManager.info("Saving...", 'Saving category.', 3000, null, null, '');
        this.categoryClient.Update(c as any, this.props.dataKey).then(response => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Update category error', 3000, null, null, '');
                return;
            }
            NotificationManager.success("Category saved.", 'Success', 3000, null, null, '');
            let responseCategory = this.responseItemToCategory(response.data.Data);
            let index = categories.findIndex(cat => cat.Id == responseCategory.Id)
            if (index > -1) {
                categories[index] = responseCategory;
            }
            this.setState({ categories: categories });
        })

    }

    create = (category: ISaveCategory) => {
        let categories = [...this.state.categories];
        NotificationManager.info("Saving...", 'Saving category.', 3000, null, null, '');
        this.categoryClient.Add(category, this.props.dataKey).then(response => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Create category error', 3000, null, null, '');
                return;
            }
            NotificationManager.success("Category saved.", 'Success', 3000, null, null, '');
            var category = this.responseItemToCategory(response.data.Data);
            categories.push(category);
            this.setState({ categories: categories });
        });
    }

    delete = (category: ICategory) => {
        let categories = [...this.state.categories];
        NotificationManager.info("Deleting...", 'Deleting category.', 3000, null, null, '');
        this.categoryClient.Delete(category.Id).then(response => {
            NotificationManager.success("Category deleted.", 'Success', 3000, null, null, '');
            let cindex = categories.findIndex(x => x.Id == category.Id);
            if (cindex > -1) {
                categories.splice(cindex, 1);
                this.setState({ categories: categories });
            }
        });
    }

    changeVisible = (category: ICategory, isVisible: boolean) => {
        category.IsHidden = !isVisible;

        var saveCategory: ISaveCategory = {
            Id: category.Id,
            Key: this.props.dataKey,
            Name: category.Name,
            Description: category.Description,
            IsHidden: category.IsHidden,
            FileAsBase64: "",
            FileName: "",
        }

        this.onSave(saveCategory)
    }



    onSave = (category: ISaveCategory) => {
        if (category.Id == 0) {
            this.create(category);
        } else {
            this.edit(category);
        }
    }

    onDelete = () => {
        if (this.state.deletingCategory) {
            this.delete(this.state.deletingCategory);
            this.setState({ deletingCategory: null });
        }
    }

    onDragEnd = (result: DropResult) => {
        if (result.source && result.destination) {
            const items = reorder(this.state.categories, result.source.index, result.destination.index);
            items.forEach((item, index) => { item.Position = index + 1 });
            this.categoryClient.Positions(items.map(item => { return { id: item.Id, position: item.Position } })).then(response => {
                this.setState({ categories: items })
            });
        }
    }



    get orderedCategories() {
        let categories = [...this.state.categories];
        return categories.sort((a, b) => a.Position - b.Position)
    }

    render() {
        return (<>
            {this.state.loading && <div className="loading" />}
            <Row>
                <Colxx xxs="12">
                    <div className="mb-2">
                        <div className="text-zero top-right-button-container">
                            <Button
                                color="primary"
                                size="lg"
                                className="top-right-button"
                                onClick={() => this.setState({ editedCategory: { Id: 0, Name: "", Description: "", FilePath: "", Position: 0, IsHidden: false, ProductsPositions: [] } })}
                            >
                                <i className="simple-icon-plus" /> ADD NEW CATEGORY
                            </Button>
                            {'  '}
                        </div>
                        <Breadcrumb heading="Categories" match={this.props.match} />
                    </div>
                    <Separator className="mb-5" />
                </Colxx>
            </Row>
            <Row>
                <Colxx xxs="12">
                    <DragDropContext onDragEnd={this.onDragEnd}>
                        <Droppable droppableId="categories">
                            {(provided, snapshot) => (
                                <div {...provided.droppableProps} ref={provided.innerRef}>
                                    {this.orderedCategories.map((p, index) => {
                                        return (
                                            <CategoryListItem onChangeVisible={(c, v) => this.changeVisible(c, v)} key={"category" + index} category={p} onDelete={() => this.setState({ deletingCategory: p })} onEdit={() => this.setState({ editedCategory: p })} onCheckItem={() => { }} index={index} />
                                        )
                                    })}
                                    {provided.placeholder}
                                </div>
                            )}
                        </Droppable>

                    </DragDropContext>

                </Colxx>
            </Row>
            <AddEditCategoryModal dataKey={this.props.dataKey} category={this.state.editedCategory} modalOpen={this.state.editedCategory != null} toggleModal={() => { this.setState({ editedCategory: null }) }} onSave={(category) => this.onSave(category)} />
            <DeleteModal isOpen={this.state.deletingCategory != null} body={`Do you really want delete category ${this.state.deletingCategory?.Name}?`} header={"Deleting category"} onDelete={() => this.onDelete()} toggle={() => this.setState({ deletingCategory: null })} />
        </>)
    }
}

export default Categories