import React from "react";
import { Row, Button, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, ButtonDropdown } from "reactstrap";
import { IEventListItemViewModel } from "../../../models/EventListItemViewModel";
import EventListItemView from "./event";
import Pagination from "../../../containers/pages/Pagination";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { AddNewEventModal } from "./addevent";
import { EventClient, IEvent } from "../../../api/eventsClient";
import { connect } from 'react-redux';
import { NotificationManager } from "../../../components/common/react-notifications";
import SubfolderListItemView from "./subfolderitem";
import AddNewSubfolder from "./subfolders/addSubfolder";
import { ContextMenu, MenuItem } from "react-contextmenu";
import EditSubfolder from "./subfolders/editSubfolder";
import IntlMessages from "../../../helpers/IntlMessages";
import { ParallaxItem } from "../../../components/parallax/parallaxItem";
import { BASE_URL } from "../../../api/settings";
import { Redirect } from 'react-router-dom'
import {
    Link,
} from "react-router-dom";
import { AuthService } from "../../../services/auth.service";


export interface Event {
    id: number,
    img: string,
    status: string,
    statusColor: string,
    date: string
}

export interface IEventDefaultViewState {
    eventData: any,
    modalOpen: boolean,
    modalEditOpen: boolean,
    currentPage: number,
    totalPage: number,
    editedSubfolder: any,
    droprownMenuShow: boolean,
    response: any,
    loading: boolean,
    nextEvent: number,
    previouseEvent: number
}

export class SubfoldersView extends React.Component<any, IEventDefaultViewState> {

    eventClient: EventClient;
    authService: AuthService = new AuthService();

    constructor(props) {
        super(props);

        this.state = {
            currentPage: 1,
            totalPage: 1,
            eventData: null,
            modalOpen: false,
            modalEditOpen: false,
            editedSubfolder: null,
            droprownMenuShow: false,
            response: null,
            loading: true,
            nextEvent: 0,
            previouseEvent: 0
        }
        this.eventClient = new EventClient();
        this.populateData();
    }

    populateData = () => {
        this.setState({ loading: true, nextEvent: 0, previouseEvent: 0 });

        this.eventClient.List(this.authService.getId()).then(response => {
            if (response.data.Data.EventsList.length == 1) {
                this.setState({
                    ...this.state,
                    nextEvent: this.props.match.params.eventId,
                    previouseEvent: this.props.match.params.eventId,
                })
            }
            var currentIndex = response.data.Data.EventsList.findIndex(x => x.Id == this.props.match.params.eventId);
            var nextIntex = (currentIndex + 1) % response.data.Data.EventsList.length;
            var previouseIndex = currentIndex == 0 ? (response.data.Data.EventsList.length - 1) : (currentIndex - 1);
            this.setState({
                ...this.state,
                nextEvent: response.data.Data.EventsList[nextIntex].Id,
                previouseEvent: response.data.Data.EventsList[previouseIndex].Id
            });
        });

        this.eventClient.EventFoldersList(this.props.match.params.eventId).then(response => {
            this.setState({ eventData: response.data.Data });
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
            } else {
                if (response.data.Data != null) {
                    this.setState({
                        currentPage: response.data.Data.Page,
                        totalPage: response.data.Data.PageCount,
                        eventData: response.data.Data,
                        response: response.data.Data
                    });
                }
            }
            this.setState({ loading: false })
        });
    }

    setModalOpen = (modalOpen: boolean, type: "Add" | "Edit") => {
        if (type == "Add") {
            this.setState({ modalOpen: modalOpen });
        } else {
            this.setState({ modalEditOpen: modalOpen });
        }
    }

    editSubfolder = (id: number) => {
        let subfolder = this.state.eventData.eventFolderList.Folders.find(x => x.FolderId == id);
        if (subfolder) {
            this.setState({ editedSubfolder: subfolder, modalEditOpen: true })
        }
    }

    deleteSubfolder = (id: number) => {

        let state = { ...this.props.state }

        let subfolder = this.state.eventData.eventFolderList.Folders.find(x => x.FolderId == id);
        if (subfolder) {
            let index = this.state.eventData.eventFolderList.Folders.indexOf(subfolder);
            if (index > -1) {
                this.eventClient.DeleteFolder(this.props.match.params.eventId, id).then((response) => {
                    if (response.data.Data) {
                        let d = [...this.state.eventData?.eventFolderList.Folders];
                        d.splice(index, 1);
                        this.setState({
                            ...this.state,
                            eventData: {
                                ...this.state.eventData,
                                eventFolderList: {
                                    ...this.state.eventData.eventFolderList,
                                    Folders: d
                                }
                            }
                        });
                        NotificationManager.success("", 'Subfolder deleted', 3000, null, null, '');
                    }
                });
            }
        }
    }

    sendToApplication = () => {
        NotificationManager.info("", 'Sending data to application...', 3000, null, null, '');
        this.eventClient.GenerateEventXML(this.props.match.params.eventId).then((response) => {
            if (response.data.Data) {
                NotificationManager.success("", 'Data sended sucessfully', 3000, null, null, '');
            } else {
                NotificationManager.error("", response.data.Error, 3000, null, null, '');
            }
        })
    }

    get parallaxData() {
        if (this.state.response == null) {
            return null;
        }

        return {
            CoverPhoto: this.state.response.eventFolderList.CoverPhoto,
            Top: this.state.response.eventFolderList.CoverOffset ? this.state.response.eventFolderList.CoverOffset : 0,
            ObjectId: Number(this.state.response.eventFolderList.EventId),
            Mode: "event",
            CanEdit: true
        }
    }

    componentDidUpdate(prevProps) {
        if (this.props.match.params.eventId != prevProps.match.params.eventId) {
            this.populateData();
        }
    }


    render() {
        if (!this.props.user) {
            return (<Redirect to={`/redirect?url=${`${BASE_URL}/Event/EventFoldersList?eventId=${(this.props as any).match.params.eventId}`}`} />)
        }

        return (<>
            {this.state.loading && <div className="loading" />}
            {!this.state.loading && <>
                <Row>
                    <Colxx xxs="6">
                        <div className="d-flex">
                            <h1>{this.state.eventData ? this.state.eventData.eventFolderList.EventName : "Event"}</h1>
                        </div>
                    </Colxx>

                    <Colxx xxs="6">
                        <div className="text-zero top-right-button-container">
                            <ButtonDropdown
                                isOpen={this.state.droprownMenuShow}
                                toggle={() => { this.setState({ droprownMenuShow: !this.state.droprownMenuShow }) }}
                                size="lg"
                            >
                                <Button
                                    style={{ fontSize: "0.8rem" }}
                                    color="primary"
                                    size="lg"
                                    className="top-right-button"
                                    onClick={() => this.setModalOpen(true, "Add")}
                                >
                                    <i className="simple-icon-plus" /> ADD NEW SUBFOLDER
                                </Button>
                                <DropdownToggle
                                    caret
                                    color="primary"
                                    className="dropdown-toggle-split btn-lg"
                                />
                                <DropdownMenu right>
                                    <DropdownItem onClick={() => this.sendToApplication()}>
                                        <i className="simple-icon-cloud-download" /> Send to application
                                    </DropdownItem>
                                </DropdownMenu>
                            </ButtonDropdown>
                        </div>
                    </Colxx>
                    <Colxx xxs="12">
                        <Separator className="mb-5" />
                    </Colxx>
                </Row>
                <Row className="mb-3">
                    <Colxx xxs="12">
                        {this.parallaxData != null && <ParallaxItem canEdit={this.parallaxData.CanEdit} coverPhoto={this.parallaxData.CoverPhoto} top={this.parallaxData.Top} mode={this.parallaxData.Mode} objectId={this.parallaxData.ObjectId} />}
                    </Colxx>
                </Row>
                <div className="disable-text-selection">
                    <Row>
                        {this.state.eventData?.eventFolderList.Folders.map((subfolder, index) => {
                            return (<SubfolderListItemView key={subfolder.FolderId} eventId={this.props.match.params.eventId} data={subfolder} onEdit={() => this.editSubfolder(subfolder.FolderId)} onDelete={() => this.deleteSubfolder(subfolder.FolderId)} />)
                        })}
                        <Pagination
                            currentPage={this.state.currentPage}
                            totalPage={this.state.totalPage}
                            onChangePage={(i) => { }}
                        />
                    </Row>
                </div>
                <AddNewSubfolder eventId={this.props.match.params.eventId} modalOpen={this.state.modalOpen} toggleModal={() => this.setModalOpen(!this.state.modalOpen, "Add")} onCreate={() => { this.populateData() }} />
                <EditSubfolder subfolder={this.state.editedSubfolder} eventId={this.props.match.params.eventId} modalOpen={this.state.modalEditOpen} toggleModal={() => this.setModalOpen(!this.state.modalEditOpen, "Edit")} onEdit={() => { this.populateData() }} />
                <ContextMenu id={"menu_id"}>
                    <MenuItem onClick={(e, data: any) => this.editSubfolder(data.folderId)} data={{ action: 'settings' }}>
                        <i className="simple-icon-docs" /> <span>Settings</span>
                    </MenuItem>

                    <MenuItem onClick={(e, data: any) => this.deleteSubfolder(data.folderId)} data={{ action: 'delete' }}>
                        <i className="simple-icon-trash" /> <span>Delete</span>
                    </MenuItem>
                </ContextMenu>
            </>}
        </>)
    }
}

const mapStateToProps = ({ authUser }) => {
    return authUser;
};

export default connect(mapStateToProps)(SubfoldersView);
