    constructor(props: any) {
        super(props);

        this.homeClient.PhotographerDetails().then((response) => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
            } else {
                this.setState({ form: response.data.Data.AddEditPhotographerPopUpViewModel })
            }

        });
    }

    formData = () => {
        return this.state.form != null ? this.state.form : this.initialValues;
    }

    onSubmit = (values, { setSubmitting }) => {
        NotificationManager.info("Saving data...", 'Saving data', 3000, null, null, '');
        this.photoClient.EditPhotographer(values).then((response) => {

            if (response.data.Data) {
                NotificationManager.success("All data saved to database", 'Data saved', 3000, null, null, '');
            } else {
                NotificationManager.error(response.data.Error, 'Saving error', 3000, null, null, '');
            }
            setSubmitting(false);

        });

    }

