import React from "react";
import { Row, Card, Badge, CustomInput, Button, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, CardBody, CardSubtitle } from "reactstrap";
import { NavLink } from 'react-router-dom';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import classnames from "classnames";

import IntlMessages from "../../../helpers/IntlMessages";

import "./index.scss";
import { HomeClient } from "../../../api/homeClient";
import { PhotoClient } from "../../../api/photoClient";
import { NotificationManager } from "../../../components/common/react-notifications";
import IconCard from "../../../components/cards/IconCard";
import SingleLightbox from "../../../components/pages/SingleLightbox";



export class UpdatesView extends React.Component<any, any> {

    homeClient: HomeClient = new HomeClient();

    constructor(props) {
        super(props);

        this.state = {
            data: { title: 'You can use photo order for', icon: 'iconsminds-clock', value: 0 },
        }
    }



    componentDidMount() {
        this.homeClient.PhotographerDetails().then(response => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
            } else {
                this.setState({
                    ...this.state.data,
                    data: {
                        value: response.data.Data.AddEditPhotographerPopUpViewModel.EventSubscription
                    }
                });
            }
        });
    }

    render() {
        return (
            <>
                <Row>
                    <Colxx xxs="12">
                        <Breadcrumb heading="Plug-ins" match={this.props.match} />
                        <Separator className="mb-5" />
                    </Colxx>
                </Row>
                <Row>
                    {
                        [...Array(3)].map((e, i) => {
                            return (
                                <Colxx sm="4" className="mb-3 pr-4 pl-4" key={i}>
                                    <Card className="mb-4">
                                        <SingleLightbox
                                            thumb="/assets/img/comingsoon.jpg"
                                            large="/assets/img/comingsoon.jpg"
                                            className="card-img-top"
                                        />

                                        <CardBody>
                                            <p className="font-weight-bold">
                                                Be patient...
                                </p>
                                            <p className="mb-3">
                                                Soon we will expand your website with additional functionalities.                                </p>
                                            {/* <p>
                                                <p className="text-muted text-small mb-2">
                                                    Price
                                                </p>
                                                <p className="mb-3">$3.99</p>
                                            </p> 
                                            <p className={"text-center"}>
                                                <Button color="primary">Buy</Button>
                                            </p>*/}
                                        </CardBody>
                                    </Card>
                                </Colxx>)
                        })
                    }


                </Row>
            </>
        );
    }
}


export default UpdatesView;