import React from 'react';
import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Input,
    Label,
    FormGroup,
    InputGroup,
    CustomInput,
    InputGroupAddon,
    InputGroupText,
    FormText,
    Row,
} from 'reactstrap';
import IntlMessages from '../../../helpers/IntlMessages';
import { Formik, Form, Field } from 'formik';
import { PhotoClient } from '../../../api/photoClient';
import { NotificationManager } from '../../../components/common/react-notifications';
import Select from 'react-select'
import { CategoryClient } from '../../../api/categoriesClient';
import { ICategory } from '../categories/category';
import { AttributesClient } from '../../../api/attributesClient';
import { IAttributeGroup, IAttribute } from '../attributes/attribute';
import { responseItemToCategory, responseItemToAttributeGroup } from '../PhotoOrderHelper';
import AttributeSelector, { IAttributeGroupItem } from './components/AttributesSelector';
import PriceRangeSelector from './components/PriceRangeSelector';

export interface IAddProductModalProps {
    modalOpen: boolean,
    toggleModal: () => void,
    onCreate: () => void
}

export interface IAddProductModalState {
    customSize: boolean,
    categories: ICategory[],
    attributeGroups: IAttributeGroup[]
}

export class AddProductModal extends React.Component<IAddProductModalProps, IAddProductModalState> {

    photoClient: PhotoClient = new PhotoClient();

    categoriesClient: CategoryClient = new CategoryClient();
    attributesClient: AttributesClient = new AttributesClient();

    initialValues = {
        Name: "",
        Size: 0,
        Description: "",
        Price: null,
        SizeSelect: 0,
        File: null,
        attributeGroups: [],
        categories: [],
        QuantityRangeMin: "",
        QuantityRangeMax: "",
        PriceRange: []
    }

    constructor(props) {
        super(props);
        this.state = {
            customSize: false,
            categories: [],
            attributeGroups: []
        }

        this.categoriesClient.List("ProductCategories").then(response => {
            let data = response.data.Data.map(x => responseItemToCategory(x))
            this.setState({ categories: data })
        });

        this.attributesClient.List("ProductAttributes").then(response => {
            let data = response.data.Data.map(x => responseItemToAttributeGroup(x))
            this.setState({ attributeGroups: data })
        });
    }



    onSubmit = async (values, formik) => {
        let formData = new FormData();
        formData.append("Description", values.Description);
        formData.append("Name", values.Name);
        formData.append("Price", "0");
        formData.append("Size", values.Size);
        formData.append("FileAsBase64", values.File);
        formData.append("QuantityRangeMin", values.QuantityRangeMin);
        formData.append("QuantityRangeMax", values.QuantityRangeMax);

        values.PriceRange.forEach((x, index) => {
            formData.append(`ProductPrices[${index}].PriceLevelFrom`, x.from);
            if (x.to) {
                formData.append(`ProductPrices[${index}].PriceLevelTo`, x.to);
            }

            formData.append(`ProductPrices[${index}].Price`, x.value);
            formData.append(`ProductPrices[${index}].IsDeleted`, "false");
        });

        values.categories.forEach(x => {
            formData.append("CategoriesIds", x.toString());
        });

        let attributesGroups: IAttributeGroupItem[] = values.attributeGroups;
        Array.prototype.concat.apply([], attributesGroups.map(x => x.attributes)).forEach((attrId: number) => {
            if (!isNaN(attrId)) {
                formData.append("AttributesIds", attrId.toString());
            }
        });

        NotificationManager.info("Saving data...", 'Saving data', 3000, null, null, '');
        this.photoClient.AddProduct(formData).then((response) => {
            if (response.data.Error) {
                NotificationManager.error("Saving error", response.data.Error, 3000, null, null, '');
            } else {
                NotificationManager.success("Data saved to database.", "Saving data success", 3000, null, null, '');
                this.props.onCreate();
                this.props.toggleModal();
            }
            formik.setSubmitting(false);
        });
    }

    get categories() {
        return this.state.categories.map(category => ({ value: category.Id, label: category.Name }));
    }

    get attributeGroups() {
        let data: any[] = [];
        this.state.attributeGroups.forEach(x => {
            data.push({
                label: x.Name,
                options: x.attributes.map(attr => ({ label: attr.Name, value: attr.Id }))
            })
        });
        return data;
    }

    render() {
        return (<Formik
            initialValues={this.initialValues}
            onSubmit={this.onSubmit}
            enableReinitialize={true}
        >
            {({
                handleSubmit,
                setFieldValue,
                setFieldTouched,
                handleChange,
                handleBlur,
                values,
                errors,
                touched,
                isSubmitting,
                submitForm
            }) => (
                <Form><Modal
                    isOpen={this.props.modalOpen}
                    toggle={this.props.toggleModal}
                    wrapClassName="modal-right"
                    backdrop="static"
                >
                    <ModalHeader toggle={this.props.toggleModal}>
                        Add product
                        </ModalHeader>
                    <ModalBody>

                        <FormGroup>
                            <Label for="Name">
                                Name
                        </Label>
                            <Field className="form-control" name="Name" type="text" id="Name" placeholder={"Name"} maxLength={20} />
                            <FormText color="muted">
                                Maximum 20 letters
                                </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label for="Description">
                                Description
                                </Label>
                            <Field className="form-control" name="Description" type="text" id="Description" placeholder={"Description"} maxLength={100} />
                            <FormText color="muted">
                                Maximum 100 letters
                                </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label for="File">
                                Image
                                </Label>
                            <InputGroup>
                                <CustomInput
                                    type="file"
                                    id="File"
                                    name="File"

                                    onChange={(event) => {
                                        if (event != null && event.currentTarget != null && event.currentTarget.files) {
                                            setFieldValue("File", event.currentTarget.files[0]);
                                        }
                                    }} />
                            </InputGroup>
                            <FormText color="muted">
                                Maximum 800x800 pixels
                                </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label for="Price">
                                Price
                                </Label>

                            <div>
                                <PriceRangeSelector initialValues={[]} onChange={(data) => { setFieldValue("PriceRange", data); }} />
                            </div>
                        </FormGroup>
                        <FormGroup>
                            <Label for="Size">
                                Size
                                 </Label>
                            <select
                                id="SizeSelect"
                                name="SizeSelect"
                                className="form-control"
                                value={values.SizeSelect}
                                onChange={handleChange}
                                onBlur={handleBlur}
                            >
                                <option disabled hidden defaultChecked></option>
                                <option value={0}>Original size</option>
                                <option value={1}>Custom size</option>
                            </select>
                            {this.state.customSize && <FormText color="muted">
                                For the longer size of photo
                                </FormText>}
                            {values.SizeSelect == 1 && <InputGroup className="mt-2">
                                <Field className="form-control" name="Size" type="number" id="Size" placeholder={"Size"} />
                                <InputGroupAddon addonType="append">
                                    <InputGroupText>px</InputGroupText>
                                </InputGroupAddon>
                            </InputGroup>}
                        </FormGroup>
                        <Row>
                            <FormGroup className="col-6 mb-0">
                                <Label>
                                    Min. quantity
                                </Label>
                                <Field className="form-control" name="QuantityRangeMin" type="number" id="QuantityRangeMin" placeholder={""} />
                            </FormGroup>
                            <FormGroup className="col-6 mb-0">
                                <Label>
                                    Max quantity
                            </Label>
                                <Field className="form-control" name="QuantityRangeMax" type="number" id="QuantityRangeMax" placeholder={""} />

                            </FormGroup>
                            <FormGroup className="col-12">
                                <FormText color="muted">
                                    Leave empty if not set
                                </FormText>
                            </FormGroup>

                        </Row>


                        <FormGroup>
                            <Label for="Categories">
                                Categories
                            </Label>
                            <Select isMulti name="colors" options={this.categories} classNamePrefix="select" onChange={(data) => { setFieldValue('categories', data ? data.map(x => x.value) : []) }} />
                        </FormGroup>
                        <FormGroup>
                            <Label for="Attributes">
                                Attributes
                                 </Label>
                            <div>
                                <AttributeSelector onChange={(data) => setFieldValue('attributeGroups', data)} attributeGroups={this.state.attributeGroups} selectedAttributeGroups={[]} />

                            </div>


                            {/* <Select isMulti name="attributes" options={this.attributeGroups} classNamePrefix="select" /> */}
                        </FormGroup>

                    </ModalBody>
                    <ModalFooter>
                        <Button color="secondary" outline onClick={this.props.toggleModal}>
                            Cancel
                            </Button>
                        <Button color="primary" type="submit" onClick={submitForm}>
                            Save
                            </Button>
                    </ModalFooter>
                </Modal>
                </Form>)
            }
        </Formik>
        );
    }
}

export default AddProductModal;
