import React from "react";
import { Row, Card, Badge, CustomInput, Button, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from "reactstrap";
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";


import "./index.scss";
import { HomeClient } from "../../../api/homeClient";
import { PhotoClient } from "../../../api/photoClient";
import { CategoryClient } from "../../../api/categoriesClient";
import { AttributesClient } from "../../../api/attributesClient";
import Tree, { TreeNode, } from "rc-tree";
import { IProduct } from "./product";
import { ICategory } from "../categories/category";
import { IAttribute, IAttributeGroup } from "../attributes/attribute";
import { DataNode, EventDataNode, Key } from "rc-tree/lib/interface";
import AddProductModal from "./addProduct";
import { ContextMenu, MenuItem } from "react-contextmenu";
import { DeleteProductModal } from "./deleteProductModal";
import EditProductModal from "./editProduct";
import { NotificationManager } from "../../../components/common/react-notifications";
import CategoryItem from "./components/CategoryItem";
import MainProductItem from "./components/MainProductItem";
import SubProductItem from "./components/SubProductItem";
import { NodeDragEventParams } from "rc-tree/lib/contextTypes";

export interface IProductState {
    products: IProduct[];
    modalOpen: boolean,
    modalEditOpen: boolean,
    modalDelete: boolean,
    deleteProductGroup?: IProduct,
    editedProductGroup?: IProduct,
    categories: ICategory[],
    attributeGroup: IAttributeGroup[];
    expandedKeys: string[],
    tree: DataNode[],
    loading: boolean,
}

export interface IData {
    categries
}

const mapAttributeItemToAttribute = (item: any, attributeGroupId: number): IAttribute => {
    return {
        Id: item.id,
        Name: item.name,
        Description: item.description,
        PriceCorrectionPercent: item.priceCorrectionPercent,
        PriceCorrectionCurrent: item.priceCorrectionCurrent,
        Position: item.position,
        AttributeGroupId: attributeGroupId
    }
}

const mapItemToAttributeGroup = (item: any): IAttributeGroup => {
    let i = {
        Id: item.id,
        Name: item.name,
        Key: item.key,
        attributes: [],
        Position: item.position
    };

    (item.attributes as any[]).forEach(attr => {
        (i.attributes as any[]).push(mapAttributeItemToAttribute(attr, i.Id));
    });

    return i;
}

const mapAttributes = (item: any[]): IAttributeGroup[] => {
    let group: IAttributeGroup[] = [];

    item.forEach(x => {
        group.push(mapItemToAttributeGroup(x))
    });

    return group;
}

const mapCategories = (item: any[]): ICategory[] => {
    let categories: ICategory[] = [];
    item.forEach(item => {
        var category: ICategory = {
            Name: item.name,
            Id: item.id,
            Description: item.description,
            FilePath: item.imageUrl,
            Position: item.position,
            IsHidden: item.isHidden,
            ProductsPositions: item.ProductsPositions
        }
        categories.push(category)
    });
    return categories;
}


export class Products extends React.Component<any, IProductState> {
    homeClient: HomeClient = new HomeClient();
    photoClient: PhotoClient = new PhotoClient();
    categoryClient: CategoryClient = new CategoryClient();
    attributeClient: AttributesClient = new AttributesClient();

    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            products: [],
            modalOpen: false,
            modalEditOpen: false,
            modalDelete: false,
            categories: [],
            attributeGroup: [],
            expandedKeys: [],
            tree: []
        }
    }

    populateData() {

    }

    setModalOpen = (modalOpen: boolean, modal: "Create" | "Edit") => {
        if (modal == "Create") {
            this.setState({ modalOpen: modalOpen });
        } else if (modal == "Edit") {
            this.setState({ modalEditOpen: modalOpen });
        }
    }

    refreshCategories = async () => {
        let categories: ICategory[] = mapCategories((await this.categoryClient.List("ProductCategories")).data.Data);
        this.setState({ categories: categories, tree: this.createTree(categories, this.state.products) });

    }

    refreshAll = async () => {
        this.setState({ loading: true });
        NotificationManager.info("Please wait.", 'Refreshing product list', 3000, null, null, '');
        let categories: ICategory[] = mapCategories((await this.categoryClient.List("ProductCategories")).data.Data);
        let products: IProduct[] = await this.getProducts();
        let attributes: IAttributeGroup[] = mapAttributes((await this.attributeClient.List("ProductAttributes")).data.Data);
        this.setState({ categories: categories, attributeGroup: attributes, products: products, tree: this.createTree(categories, products) });
        NotificationManager.info("Your product list is up to date.", 'Refreshing ended', 3000, null, null, '');
        this.setState({ loading: false });
    }

    async componentDidMount() {
        this.setState({ loading: true });
        let categories: ICategory[] = mapCategories((await this.categoryClient.List("ProductCategories")).data.Data);
        let attributes: IAttributeGroup[] = mapAttributes((await this.attributeClient.List("ProductAttributes")).data.Data);
        let products: IProduct[] = await this.getProducts();
        this.setState({ categories: categories, attributeGroup: attributes, products: products, tree: this.createTree(categories, products), loading: false });
    }

    isCollapsed = (key: string) => {
        return this.state.expandedKeys.indexOf(key) == -1;
    }

    changeCollapse = (key: string) => {
        let data = [...this.state.expandedKeys];
        if (this.state.expandedKeys.indexOf(key) > -1) {
            data = data.filter(x => !x.startsWith(key));
        } else {
            data.push(key);
        }
        this.setState({ expandedKeys: data });
    }

    createTree = (categories: ICategory[], products: IProduct[]): DataNode[] => {
        let nodes: DataNode[] = [];
        categories.forEach(c => {
            var categoryNode: any = {
                key: c.Id.toString(),
                title: (n) => <CategoryItem key={`cat-${n.key}`} isCollapsed={this.isCollapsed(n.key.toString())} category={n.category} onChangeVisible={(product, visible) => { }} onCollapse={(id) => this.changeCollapse(n.key.toString())} onDelete={() => { }} onEdit={() => { }} />,
                children: [],
                category: c,
                className: "node-category"
            }

            var categoryProducts: IProduct[] = products.filter(p => p.CategoriesIds.indexOf(c.Id) > -1 && p.MainProductId == null);
            var orderedCP = categoryProducts;
            if (c.ProductsPositions && c.ProductsPositions.length) {
                orderedCP = [];
                c.ProductsPositions.forEach(p => {
                    let finded = categoryProducts.findIndex(x => x.Id == p);
                    if (finded > -1) {
                        orderedCP.push(categoryProducts[finded]);
                        categoryProducts.splice(finded, 1);
                    }
                });
                orderedCP = orderedCP.concat(categoryProducts);
            }
            orderedCP.forEach(product => {
                var productNode: any = {
                    key: `${c.Id}-${product.Id}`,
                    title: (n) => <div style={{ paddingLeft: "3rem" }}><MainProductItem key={`mainprod-${n.key}`} product={n.product} onChangeVisible={(product, visible) => this.onChangeProductVisible(product, visible)} attributesGroups={this.state.attributeGroup} categories={this.state.categories} isCollapsed={this.isCollapsed(n.key.toString())} onCollapse={(id) => this.changeCollapse(n.key.toString())} onDelete={(p) => this.onDeleteRequest(p)} onEdit={(product) => this.onEdit(product)} /></div>,
                    children: [],
                    product: product,
                    className: "node-product"
                }

                if (product.products) {
                    product.products.forEach(subProduct => {
                        var subProductNode: any = {
                            key: `${c.Id}-${product.Id}-${subProduct.Id}`,
                            title: (n) => <div style={{ paddingLeft: "6rem" }}><SubProductItem key={`mainprod-${n.key}`} product={n.product} onChangeVisible={(product, visible) => this.onChangeProductVisible(product, visible)} attributesGroups={this.state.attributeGroup} categories={this.state.categories} isCollapsed={this.isCollapsed(n.key.toString())} onCollapse={(id) => this.changeCollapse(n.key.toString())} onDelete={(p) => this.onDeleteRequest(p)} onEdit={(product) => this.onEdit(product)} /></div>,
                            children: [],
                            product: subProduct,
                            className: "node-subproduct"
                        }
                        productNode.children.push(subProductNode);
                    });
                }



                categoryNode.children.push(productNode);
            });
            nodes.push(categoryNode);
        });

        if (products.filter(x => x.CategoriesIds.length == 0).length > 0) {
            var emptyCategory: ICategory = { Id: 9999, Name: "Others", IsHidden: false, Description: "", FilePath: "", Position: 999999 }
            var categoryNode: any = {
                key: emptyCategory.Id.toString(),
                title: (n) => <CategoryItem key={`cat-${n.key}`} isCollapsed={this.isCollapsed(n.key.toString())} category={n.category} onChangeVisible={(product, visible) => { }} onCollapse={(id) => this.changeCollapse(n.key.toString())} onDelete={() => { }} onEdit={() => { }} />,
                children: [],
                category: emptyCategory,
                className: "node-category"
            }

            products.filter(x => x.CategoriesIds.length == 0).forEach(product => {
                var productNode: any = {
                    key: `${emptyCategory.Id}-${product.Id}`,
                    title: (n) => <div style={{ paddingLeft: "3rem" }}><MainProductItem key={`mainprod-${n.key}`} product={n.product} onChangeVisible={(product, visible) => this.onChangeProductVisible(product, visible)} attributesGroups={this.state.attributeGroup} categories={this.state.categories} isCollapsed={this.isCollapsed(n.key.toString())} onCollapse={(id) => this.changeCollapse(n.key.toString())} onDelete={(p) => this.onDeleteRequest(p)} onEdit={(product) => this.onEdit(product)} /></div>,
                    children: [],
                    product: product,
                    className: "node-product"
                }

                if (product.products) {
                    product.products.forEach(subProduct => {
                        var subProductNode: any = {
                            key: `${emptyCategory.Id}-${product.Id}-${subProduct.Id}`,
                            title: (n) => <div style={{ paddingLeft: "6rem" }}><SubProductItem key={`mainprod-${n.key}`} product={n.product} onChangeVisible={(product, visible) => this.onChangeProductVisible(product, visible)} attributesGroups={this.state.attributeGroup} categories={this.state.categories} isCollapsed={this.isCollapsed(n.key.toString())} onCollapse={(id) => this.changeCollapse(n.key.toString())} onDelete={(p) => this.onDeleteRequest(p)} onEdit={(product) => this.onEdit(product)} /></div>,
                            children: [],
                            product: subProduct,
                            className: "node-subproduct"
                        }
                        productNode.children.push(subProductNode);
                    });
                }

                categoryNode.children.push(productNode);
            });
            nodes.push(categoryNode);

        }

        return nodes;
    }

    getProducts = async () => {
        let data = await this.photoClient.ListProducts();
        let responseProducts: IProduct[] = data.data.Data;
        let productDictionary = {}
        responseProducts.filter(x => x.MainProductId == null).forEach(mainProduct => {
            productDictionary[mainProduct.Id] = mainProduct;
        });
        responseProducts.filter(x => x.MainProductId != null).forEach(subProduct => {
            if (productDictionary[subProduct.MainProductId]) {
                if ((productDictionary[subProduct.MainProductId] as IProduct).products == null) {
                    (productDictionary[subProduct.MainProductId] as IProduct).products = [];
                }
                (productDictionary[subProduct.MainProductId] as IProduct).products.push(subProduct);
            }
        });
        var products: IProduct[] = Object.values(productDictionary).map(x => x) as IProduct[];
        return products;
    }

    onDeleteRequest = (product: IProduct) => {
        this.setState({
            modalDelete: true, deleteProductGroup: product
        });
    }

    onEdit = (productGroup: IProduct) => {
        this.setState({ editedProductGroup: productGroup, modalEditOpen: true });
    }


    onDelete = async () => {

        let product = this.state.deleteProductGroup;
        if (product == null) {
            return;
        }

        NotificationManager.info("Product is deleting...", 'Deleting product', 3000, null, null, '');
        var response = await this.photoClient.DeleteProduct(product.Id);
        await this.refreshAll();
        NotificationManager.success("Product is deleted.", 'Product deleted sucessfully', 3000, null, null, '');
    }

    refresh = () => {
        this.refreshAll();
    }

    onChangeProductVisible = async (product: IProduct, visible: boolean) => {
        NotificationManager.info("Changing product visible...", 'Changing state', 3000, null, null, '');
        var response = await this.photoClient.SetProductVisible(product.Id, visible);
        this.refresh();
        NotificationManager.success("Product visible changed sucessfully.", 'Product state changed', 3000, null, null, '');
    }

    allowDrop = (options): any => {
        if (this.draggingNode == null || options.dropPosition < 0) {
            return false;
        }

        return this.draggingNode.className == "node-product" && (options.dropNode.className == "node-category" || options.dropNode.className == "node-product");
    }

    isDraggable = (node: DataNode): boolean => {
        if (node.className == "node-subproduct") {
            return false;
        }
        return true;
    }

    getItemKeys = (node: EventDataNode): { categoryId: number, productId: number } => {
        var categoryId, productId;
        [categoryId, productId] = (node.key as string).split("-");
        return {
            categoryId,
            productId
        }
    }

    isReorder = (dragNode: EventDataNode, dropNode: EventDataNode) => {
        let dragItemKeys = this.getItemKeys(dragNode);
        let dropItemKeys = this.getItemKeys(dropNode);
        return dragItemKeys.categoryId == dropItemKeys.categoryId;
    }

    isClone = (dragNode: EventDataNode, dropNode: EventDataNode) => {
        let dragItemKeys = this.getItemKeys(dragNode);
        let dropItemKeys = this.getItemKeys(dropNode);
        return dragItemKeys.categoryId != dropItemKeys.categoryId;
    }

    arraymove = (arr, fromIndex, toIndex) => {
        var element = arr[fromIndex];
        arr.splice(fromIndex, 1);
        arr.splice(toIndex, 0, element);
    }

    onDrop = async (options: { dragNode: EventDataNode, dragNodesKeys: Key[], dropPosition: number, dropToGap: boolean, node: EventDataNode }) => {
        if (this.isReorder(options.dragNode, options.node)) {
            let position = 0;
            if (options.node.className == "node-product") {
                position = options.dropPosition;
                if (position == 0) {
                    position = 1;
                }
            }
            else if (options.node.className == "node-category") {
                let categoryIndex = this.state.categories.findIndex(x => x.Id == (options.node as any).category.Id);
                if (categoryIndex == options.dropPosition) {
                    position = 0;
                } else if (categoryIndex < options.dropPosition) {
                    position = -1;
                }
            }
            var data = this.getItemKeys(options.dragNode);
            var treeCategory = this.state.tree.find(x => x.key == data.categoryId);
            if (treeCategory) {
                var productIds = (treeCategory.children as any).map(x => x.product.Id);

                if (position == -1) {
                    position = productIds.length - 1;
                }

                let moveIndex = productIds.indexOf(Number(data.productId));
                if (moveIndex > -1) {
                    this.arraymove(productIds, moveIndex, position);
                    NotificationManager.info('Please wait.', "Updating positions...", 3000, null, null, '');
                    await this.categoryClient.UpdateCategoryProductsPositions(Number(data.categoryId), productIds);
                    await this.refreshCategories();
                    NotificationManager.success('Position updated.', "Positions updated sucessfully.", 3000, null, null, '');
                }


            }


        }
        else if (this.isClone(options.dragNode, options.node)) {
            let position: number | null = -1;
            let categoryId = -1;
            let productId = -1;
            let item = this.getItemKeys(options.node);
            let dragItem = this.getItemKeys(options.dragNode);
            if (options.node.className == "node-product") {
                position = options.dropPosition;
                if (position == 0) {
                    position = 1;
                }
                categoryId = item.categoryId;
                productId = dragItem.productId;

            }
            else if (options.node.className == "node-category") {
                let categoryIndex = this.state.categories.findIndex(x => x.Id == (options.node as any).category.Id);
                let category = this.state.categories[categoryIndex];
                if (categoryIndex == options.dropPosition) {
                    position = 1;
                } else if (categoryIndex < options.dropPosition) {
                    position = -1;
                }
                categoryId = category.Id;
                productId = dragItem.productId;
            }
            console.log(productId, categoryId, position);
            if (position < 1) {
                position = null;
            }
            NotificationManager.info("Please wait", 'Cloning product...', 3000, null, null, '');
            await this.categoryClient.CloneProductToCategory(productId, categoryId, position);
            await this.refreshAll();
            NotificationManager.success("Cloning ended.", "Product cloned sucessfully", 3000, null, null, '');
            //await this.categoryClient.CloneProductToCategory()

            //console.log("clone", position item.productId)
        }

    }

    draggingNode: EventDataNode | null = null;

    onDragStart = (info: NodeDragEventParams) => {
        this.draggingNode = info.node;
    }



    render() {
        return (
            <>
                {this.state.loading && <div className="loading" />}
                <Row>
                    <Colxx xxs="12">
                        <div className="mb-2">
                            <div className="text-zero top-right-button-container">
                                <Button
                                    color="primary"
                                    size="lg"
                                    className="top-right-button"
                                    onClick={() => this.setModalOpen(true, "Create")}
                                >
                                    <i className="simple-icon-plus" /> ADD NEW PRODUCT
                                </Button>
                                {'  '}
                            </div>
                            <Breadcrumb heading="Products" match={this.props.match} />
                        </div>
                        <Separator className="mb-5" />
                    </Colxx>
                </Row>
                <div className="draggable-container">
                    <Tree
                        onDragStart={(info) => this.onDragStart(info)}
                        onDrop={this.onDrop}
                        draggable={(node) => this.isDraggable(node)}
                        treeData={this.state.tree}
                        expandedKeys={this.state.expandedKeys}
                        autoExpandParent={true}
                        allowDrop={this.allowDrop}
                        dropIndicatorRender={(props) => <div>Place here</div>}
                    ></Tree>
                </div>

                <ContextMenu id="product_menu" onShow={(e) => { }} >
                    <MenuItem onClick={(e, data) => { this.onEdit((data as any).product) }} data={{ action: 'settings' }}>
                        <i className="simple-icon-docs" /> <span>Edit</span>
                    </MenuItem>

                    <MenuItem onClick={(e, data) => { this.onDeleteRequest((data as any).product) }} data={{ action: 'delete' }}>
                        <i className="simple-icon-trash" /> <span>Delete</span>
                    </MenuItem>
                </ContextMenu>
                <AddProductModal modalOpen={this.state.modalOpen} toggleModal={() => this.setModalOpen(!this.state.modalOpen, "Create")} onCreate={this.refresh} />
                {this.state.editedProductGroup && <EditProductModal modalOpen={this.state.modalEditOpen} toggleModal={() => this.setModalOpen(!this.state.modalEditOpen, "Edit")} onEdit={this.refresh} product={this.state.editedProductGroup as IProduct} />}
                <DeleteProductModal title={"Product delete"} description={"Do you really want delete product?"} toggle={(show) => this.setState({ modalDelete: show })} onDelete={() => this.onDelete()} isOpen={this.state.modalDelete} />
            </>
        )
    }
}

export default Products;