import Axios, { AxiosRequestConfig } from "axios";
import * as globalSettings from "./settings";
import { IApiResponse } from "./IApiResponse";
import { layer } from "@fortawesome/fontawesome-svg-core";
import { ICategory, ISaveCategory } from "../views/photoOrder/categories/category";

export class CategoryClient {
    settings: AxiosRequestConfig = {};

    constructor() {
        let settings = { ...globalSettings.CONFIG }
        // settings.withCredentials = false;
        if (window.localStorage.getItem("loginData")) {
            let data = JSON.parse(window.localStorage.getItem("loginData") as string);

            settings.headers["Authorization"] = data.Token;
            this.settings = settings;
        }
    }

    List(key: string) {
        var config = {
            ...this.settings,
        }
        return Axios.get<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/category/getcategory?key=${key}`, this.settings);
    }

    Add(category: ISaveCategory, key: string) {
        return Axios.post<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/category/CreateNewCategory`, category, this.settings);
    }

    Delete(categoryId: number) {
        return Axios.delete<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/category/DeleteCategory?id=${categoryId}`, this.settings);

    }

    Update(category: ISaveCategory, key: string) {
        return Axios.put<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/category/UpdateCategory`, category, this.settings);
    }

    Positions(positions: { id: number, position: number }[]) {
        let s = { ...this.settings }
        s.headers["Content-Type"] = "application/json";
        return Axios.put<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/category/UpdateCategoryPosition`, positions, s);
    }

    CloneProductToCategory(baseProductId: number, categoryId: number, position?: number | null) {
        let s = { ...this.settings }
        s.headers["Content-Type"] = "application/json";
        return Axios.post<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/category/CloneProductToCategory`, { CategoryId: categoryId, BaseProductId: baseProductId, Position: position }, s);
    }

    UpdateCategoryProductsPositions(categoryId: number, productIds: number[]) {
        let s = { ...this.settings }
        s.headers["Content-Type"] = "application/json";
        return Axios.put<IApiResponse<any>>(`${globalSettings.BASE_URL}/api2/category/UpdateCategoryProductsPositions`, { CategoryId: categoryId, ProductsIdAsc: productIds }, s);

    }
}