import React from "react";
import { Colxx } from "../../../components/common/CustomBootstrap";
import { Card, CardImg, CardBody, Row, CardSubtitle, Badge, CardText, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from "reactstrap";
import { IEventListItemViewModel } from "../../../models/EventListItemViewModel";
import { NavLink } from "react-router-dom"
import { ContextMenu, MenuItem, ContextMenuTrigger } from 'react-contextmenu';
import classnames from "classnames";
import IntlMessages from "../../../helpers/IntlMessages";
import { connect } from 'react-redux';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import "./event.scss";
import { EventClient, IEvent } from "../../../api/eventsClient";
import { IAuthUserState } from "../../../redux/auth/authUserState";
import { NotificationManager } from "../../../components/common/react-notifications";
import {
    Redirect,
} from 'react-router-dom';
import { BASE_URL } from "../../../api/settings";

export interface IEventListItemViewProps {
    event: IEvent,
    onDelete: (event: IEvent) => void;
    onEdit: (event: IEvent) => void
}

type IProps = IEventListItemViewProps & IAuthUserState

export class EventListItemView extends React.Component<IProps, any> {

    eventClient: EventClient;

    state = {
        isImage: true,
        data: {}
    }

    constructor(props: IProps) {
        super(props);
        this.eventClient = new EventClient();
    }


    onImageError = () => {
        this.setState({ isImage: false })
    }

    onCopyDirectLink = (e) => {
        e.preventDefault();

    }

    onEdit = (e) => {
        e.preventDefault();
        console.log('onedit', this.props.event);
        this.props.onEdit(this.props.event);

    }

    onDelete = (e) => {
        e.preventDefault();
        this.props.onDelete(this.props.event);
    }

    directLink = (): string => {
        let key = this.props.event.NoPassKey;
        if (key == null) {
            key = ""
        }
        return `${window.location.origin}/portal/direct/${key}`
    }

    render() {

        return (<Colxx sm="6" lg="4" xl="3" className="mb-3 eventListItem" key={this.props.event.Id}>
            <ContextMenuTrigger id="menu_id" collect={(props) => { return { data: props.data, event: this.props.event } }}>
                <NavLink to={`/photo-hosting/event/${this.props.event.Id}`} className="w-40 w-sm-100">
                    <Card onClick={() => { }}>
                        <div className="position-relative">
                            <CardImg top alt={this.props.event.Name} src={this.state.isImage ? this.props.event.FileSrc : "https://via.placeholder.com/600?text=No%20image"} onError={(e => this.onImageError())} />

                            {
                                this.props.event.PassProtected && <Badge color="danger" pill className="position-absolute badge-top-left">PASSWORD</Badge>
                            }
                            {
                                this.props.event.IsHidden && <Badge color="primary" pill className={classnames("position-absolute", { "badge-top-left": !this.props.event.PassProtected, "badge-top-left-2": this.props.event.PassProtected })}>HIDDEN</Badge>
                            }

                            <div className="eventListItem__menu">
                                <UncontrolledDropdown>
                                    <DropdownToggle size="xs" color="primary" onClickCapture={e => e.preventDefault()}>
                                        <i className="simple-icon-menu" />
                                    </DropdownToggle>
                                    <DropdownMenu>
                                        <CopyToClipboard text={this.directLink()} onCopy={() => { NotificationManager.success("URL copiedd to clipboard", "", 3000, null, null, ''); }}>
                                            <DropdownItem onClick={(e) => { e.preventDefault() }}>
                                                <i className="simple-icon-link" /> <span>Copy direct link</span>
                                            </DropdownItem>
                                        </CopyToClipboard>
                                        <DropdownItem onClick={(e) => this.onEdit(e)}>
                                            <i className="simple-icon-docs" /> <span>Settings</span>
                                        </DropdownItem>
                                        <DropdownItem onClick={(e) => this.onDelete(e)}>
                                            <i className="simple-icon-trash" /> <span>Delete</span>
                                        </DropdownItem>
                                    </DropdownMenu>
                                </UncontrolledDropdown>
                            </div>
                        </div>
                        <CardBody>
                            <CardSubtitle className="mb-4">
                                {this.props.event.Name}
                            </CardSubtitle>
                        </CardBody>

                    </Card>
                </NavLink>
            </ContextMenuTrigger>

        </Colxx >)
    }
}

const mapStateToProps = ({ authUser }) => {
    return authUser;
};


export default connect(mapStateToProps)(EventListItemView);
