import React from 'react';
import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Input,
    Label,
    FormGroup,
    InputGroup,
    CustomInput,
    InputGroupAddon,
    InputGroupText,
    FormText,
} from 'reactstrap';
import IntlMessages from '../../../helpers/IntlMessages';
import { Formik, Form, Field } from 'formik';
import { PhotoClient } from '../../../api/photoClient';
import { NotificationManager } from '../../../components/common/react-notifications';
import { IAttributeGroup } from './attribute';

export interface IAddEditAttributeGroupModalProps {
    modalOpen: boolean,
    toggleModal: () => void,
    onSave: (category: IAttributeGroup) => void,
    attributeGroup: IAttributeGroup | null
}

export class AddEditAttributeGroupModal extends React.Component<IAddEditAttributeGroupModalProps, any> {
    _initialValues = {
        Name: "",
        Id: 0
    }

    constructor(props) {
        super(props);
        this.state = {
            customSize: false
        }
    }

    get initialValues() {
        if (this.props.attributeGroup) {
            return {
                Name: this.props.attributeGroup.Name,
                Id: this.props.attributeGroup.Id,
                File: null,
                Key: this.props.attributeGroup.Key
            }
        }
        return this._initialValues;
    }

    onSubmit = (values, formik) => {
        this.props.onSave({ Id: values.Id, Position: 0, Name: values.Name, attributes: this.props.attributeGroup ? this.props.attributeGroup.attributes : [], Key: values.Key });
        this.props.toggleModal();
        formik.setSubmitting(false);
    }

    render() {
        return (<Formik
            initialValues={this.initialValues}
            onSubmit={this.onSubmit}
            enableReinitialize={true}
        >
            {({
                handleSubmit,
                setFieldValue,
                setFieldTouched,
                handleChange,
                handleBlur,
                values,
                errors,
                touched,
                isSubmitting,
                submitForm
            }) => (
                <Form><Modal
                    isOpen={this.props.modalOpen}
                    toggle={this.props.toggleModal}
                    wrapClassName="modal-right"
                    backdrop="static"
                >
                    <ModalHeader toggle={this.props.toggleModal}>
                        {this.props.attributeGroup?.Id == 0 ? "Add attribute group" : "Edit attribute group"}
                    </ModalHeader>
                    <ModalBody>

                        <FormGroup>
                            <Label for="Name">
                                Name
                                </Label>
                            <Field className="form-control" name="Name" type="text" id="Name" placeholder={"Name"} maxLength={20} />
                            <FormText color="muted">
                                Maximum 20 letters
                                </FormText>
                        </FormGroup>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="secondary" outline onClick={this.props.toggleModal}>
                            Cancel
                            </Button>
                        <Button color="primary" type="submit" onClick={submitForm}>
                            Save
                            </Button>
                    </ModalFooter>
                </Modal>
                </Form>)
            }
        </Formik>
        );
    }
}

export default AddEditAttributeGroupModal;
