import React from "react";
import { Button, InputGroup, InputGroupAddon, InputGroupText, Row } from "reactstrap";
import { Colxx } from "../../../../components/common/CustomBootstrap";
import * as _ from "underscore";

export interface IPriceRange {
    from?: number,
    to?: number,
    value: number
}

export interface IPriceRangeSelectorProps {
    //priceRange: IPriceRange[]
    onChange: (priceRange: IPriceRange[]) => void;
    initialValues: IPriceRange[];
}

export interface IPriceRangeSelectorState {
    priceRange: IPriceRange[]
}

export class PriceRangeInput extends React.Component<{ from?: number, onChange: (data: IPriceRange) => void, onRemoveLast: () => void }, { to?: number, price?: number }> {

    constructor(props) {
        super(props);
        this.state = {
            price: undefined,
            to: undefined
        }
    }

    onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        let state = { ...this.state }
        if (e.target.value) {
            state[e.target.name] = e.target.valueAsNumber
        } else {
            state[e.target.name] = null;
        }

        this.setState(state);
    }

    isButtonDisabled = () => {
        //return !this.state.price || (this.state.to != null && this.state.to != 0 && this.state.to < this.props.from)
        return !this.state.price || (this.state.to != null && this.props.from != undefined && this.state.to < this.props.from);
    }

    onAddPriceRange = () => {
        this.props.onChange({ from: this.props.from, to: (this.state.to == 0 ? null : this.state.to) as number, value: this.state.price as number });
        this.clear();
    }

    get isInfinity() {
        return !this.props.from;
    }

    clear = () => {

        let state = {}
        this.setState(state);
    }

    render() {
        return (
            <>
                {!this.isInfinity && <Row>
                    <Colxx xxs={12}>
                        <InputGroup>
                            <input value={this.state.price} name="price" type="number" placeholder="Price" className="form-control" onChange={(event) => this.onChange(event)} />
                            <InputGroupAddon addonType="append">
                                <InputGroupText>&euro;</InputGroupText>
                            </InputGroupAddon>
                        </InputGroup>
                    </Colxx>
                </Row>}
                <Row>
                    {!this.isInfinity && <> <Colxx xxs={5} className="pr-0">
                        <InputGroup>
                            <input value={this.props.from} disabled type="number" placeholder="From" className="form-control" />
                            <InputGroupAddon addonType="prepend">
                                <InputGroupText>Piece</InputGroupText>
                            </InputGroupAddon>
                        </InputGroup>
                    </Colxx>
                        <Colxx xxs={1} className="d-flex justify-content-center align-items-center p-0">-</Colxx>
                        <Colxx xxs={6} className="pl-0">
                            <InputGroup>
                                <input value={this.state.to} name="to" type="number" placeholder="To" className="form-control" onChange={(event) => this.onChange(event)} />
                                <InputGroupAddon addonType="prepend">
                                    <InputGroupText>Piece</InputGroupText>
                                </InputGroupAddon>
                            </InputGroup>
                        </Colxx>
                    </>}
                    <Colxx xxs={6} className="mt-1">
                        {!this.isInfinity &&
                            <Button disabled={this.isButtonDisabled()} color="primary" outline onClick={() => this.onAddPriceRange()}><i className="simple-icon-plus" /> Add range</Button>
                        }
                    </Colxx>
                    <Colxx xxs={6} className="mt-1 text-right">
                        <Button color="danger" outline onClick={() => this.props.onRemoveLast()}><i className="simple-icon-trash" /> Remove</Button>
                    </Colxx>
                </Row>
            </>
        )
    }
}

export class PriceRangeSelector extends React.Component<IPriceRangeSelectorProps, IPriceRangeSelectorState>
{
    constructor(props) {
        super(props);
        this.state = {
            priceRange: []
        }
    }

    componentDidMount() {
        this.setState({ priceRange: this.props.initialValues });
    }

    componentDidUpdate(prevProps: IPriceRangeSelectorProps, prevState: IPriceRangeSelectorState) {
        if (!_.isEqual(prevProps.initialValues, this.props.initialValues)) {
            this.setState({ priceRange: this.props.initialValues });
        }
    }

    addPriceRange = (data: IPriceRange) => {
        let ranges = [...this.state.priceRange];
        ranges.push(data);
        this.setState({ priceRange: ranges }, () => {
            this.props.onChange(this.state.priceRange);
        });
    }

    lastFrom = () => {
        if (this.state.priceRange.length) {
            if (this.state.priceRange[this.state.priceRange.length - 1].to) {
                return this.state.priceRange[this.state.priceRange.length - 1].to as number + 1;
            } else {
                return undefined;
            }

        }
        return 1;
    }

    removeLast = () => {
        let items = [...this.state.priceRange];
        items.pop();
        this.setState({ priceRange: items }, () => {
            this.props.onChange(this.state.priceRange);
        });
    }

    render() {
        return (<>
            <Row>
                {
                    this.state.priceRange.map((range, index) => {
                        return (
                            <Colxx key={"rang-" + index} xxs={12}>
                                <Row>
                                    <Colxx xxs={8}>From <strong>{range.from}</strong> to <strong>{(range.to ? range.to : "infinity")}</strong> Pieces</Colxx>
                                    <Colxx xxs={4} className={"pl-0 text-right"}><strong>{range.value} &euro;</strong></Colxx>
                                </Row>
                            </Colxx>
                        );
                    })
                }

            </Row>
            <PriceRangeInput from={this.lastFrom()} onChange={(data) => this.addPriceRange(data)} onRemoveLast={() => this.removeLast()} />
        </>)
    }
}

export default PriceRangeSelector;