import React from "react";
import { Row, Card, Badge, CustomInput, Button, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, CardBody } from "reactstrap";
import { NavLink } from 'react-router-dom';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import classnames from "classnames";

import IntlMessages from "../../../helpers/IntlMessages";

import "./index.scss";
import { HomeClient } from "../../../api/homeClient";
import { PhotoClient } from "../../../api/photoClient";
import { NotificationManager } from "../../../components/common/react-notifications";
import IconCard from "../../../components/cards/IconCard";



export class OverviewView extends React.Component<any, any> {

    homeClient: HomeClient = new HomeClient();

    constructor(props) {
        super(props);

        this.state = {
            data: { title: 'You can use photo order for', icon: 'iconsminds-clock', value: 0 },
        }
    }



    componentDidMount() {
        this.homeClient.PhotographerDetails().then(response => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
            } else {
                this.setState({
                    ...this.state.data,
                    data: {
                        value: response.data.Data.AddEditPhotographerPopUpViewModel.EventSubscription
                    }
                });
            }
        });
    }

    render() {
        return (
            <>
                <Row>
                    <Colxx xxs="12">
                        <Breadcrumb heading="Overview" match={this.props.match} />
                        <Separator className="mb-5" />
                    </Colxx>
                </Row>
                <Row>
                    <Colxx xxs="12" className="mb-3">
                        <Card className="mb-4">
                            <CardBody>
                                <Row>
                                    <Colxx sm="6">
                                        <img className="img-fluid border-radius" src="/assets/img/photoorder.jpg" />

                                    </Colxx>
                                    <Colxx sm="6" className={"d-flex"} style={{ flexDirection: "column" }}>
                                        <h3 style={{ fontWeight: "bold" }}>Photo Order - Best mobile photo kiosk</h3>
                                        <p>
                                            If you have a printer and want to increase the number of printed photos, the Photo Order system is the perfect solution for you.
                                            <br />It allows your customers to send photos directly from their phone, informs about the price of the order and at the same time informs about special offers.
                                            <br />It has never been so easy and fast to transfer photos from your phone.
                                            <br />In addition, we will provide you with a special link that will expand your current website with a fully functional e-shop.
                                            <br />Now your customers can send, pay, receive photos and more right from home.
                                            <br /><br /><strong>Be online 24/7 !!!</strong>
                                            <br /><br />Interested? Contact us !!!
                                        </p>
                                        <Row style={{ flex: "1 1 auto", display: "flex", justifyContent: "center", alignItems: "center" }}>
                                            <Colxx xss="12">
                                                <div className='icon-cards-row text-center'>
                                                    <div key={`icon_card_0`}>
                                                        <i className={'iconsminds-clock'} />
                                                        <p className="card-text font-weight-semibold mb-0">
                                                            You can use photo order for
                                                        </p>
                                                        <p className="lead text-center">{this.state.data.value} days</p>
                                                    </div>
                                                </div>
                                            </Colxx>
                                        </Row>
                                    </Colxx>
                                </Row>
                            </CardBody>
                        </Card>
                    </Colxx>
                </Row>
            </>
        );
    }
}


export default OverviewView;