import { Formik, useFormik } from 'formik';
import React, { useEffect, useState } from 'react';
import { Button, Card, CardBody, CardTitle, CustomInput, FormGroup, FormText, InputGroup, InputGroupAddon, Label, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap';
import { AdvertisementClient, AdvertisementDTO, FileClient, FileDTO, MultimediaTypesEnum } from '../../../../api/ApiClient';
import { API_BASE } from '../../../../api/ApiSettings';
import { NotificationManager } from '../../../../components/common/react-notifications';

export function AdvertisementForm(props: any) {
    const advertisementClient = new AdvertisementClient(API_BASE);
    const fileClient = new FileClient(API_BASE);

    const formik = useFormik({
        initialValues: new AdvertisementDTO({ id: 0, photographerId: 0 }),
        onSubmit: async values => {
            await onSubmit(values);
        },
    });


    useEffect(() => {
        formik.setValues(props.advertisement, false);
    }, []);

    async function onSubmit(advertisement: AdvertisementDTO) {
        NotificationManager.info("Saving...", 'Saving banner.', 3000, null, null, '');
        var file: File = (advertisement as any).file;
        var mobileFile: File = (advertisement as any).mobileFile;

        var fileResponse: FileDTO | null = null;
        var mobileFileResponse: FileDTO | null = null;


        if (file) {
            fileResponse = await fileClient.upload({ data: file, fileName: file.name }, MultimediaTypesEnum.AdvertisementSlider);
        }

        if (mobileFile) {
            mobileFileResponse = await fileClient.upload({ data: mobileFile, fileName: mobileFile.name }, MultimediaTypesEnum.AdvertisementSlider);
        }

        if (fileResponse) {
            advertisement.fileGuid = fileResponse.guid;
            advertisement.fileName = fileResponse.name;
            advertisement.imageUrl = fileResponse.url;
        }

        if (mobileFileResponse) {
            advertisement.mobileFileGuid = mobileFileResponse.guid;
            advertisement.mobileFileName = mobileFileResponse.name;
            advertisement.mobileImageUrl = mobileFileResponse.url;
        }


        if (advertisement.id) {
            await advertisementClient.update(advertisement);
        } else {
            advertisement.id = 0;
            advertisement.photographerId = 0;
            await advertisementClient.create(advertisement);

        }
        NotificationManager.success("Banner saved.", 'Success', 3000, null, null, '');
        props.onClose();
    }

    function openFile(url: string | null | undefined) {
        if (url) {
            window.open(url as string, "_blank");
        }
    }

    return (
        <form onSubmit={formik.handleSubmit}>
            <Modal
                isOpen={true}
                wrapClassName="modal-right"
                backdrop="static"
            >
                <ModalHeader toggle={() => props.onClose()}>
                    {formik.values.id ? "Edit banner" : "Add banner"}
                </ModalHeader>
                <ModalBody>

                    <FormGroup>
                        <Label for="buttonText">
                            Description
                        </Label>
                        <input onChange={formik.handleChange} className="form-control" type="text" placeholder="" id="buttonText" name="buttonText" value={formik.values.buttonText || ""} />
                        <FormText color="muted">
                            Maximum 100 letters
                        </FormText>
                    </FormGroup>
                    <FormGroup>
                        <Label for="File">
                            Image
                        </Label>
                        <InputGroup>
                            <CustomInput
                                type="file"
                                id="file"
                                name="file"
                                onChange={(event) => {
                                    if (event != null && event.currentTarget != null && event.currentTarget.files) {
                                        formik.setFieldValue("file", (event.currentTarget as any).files[0], false)
                                    }
                                }} />
                            {formik.values.imageUrl ? <InputGroupAddon addonType="append" onClick={() => openFile(formik.values.imageUrl)}>Show</InputGroupAddon> : null}
                        </InputGroup>
                        <FormText color="muted">
                            Maximum 1920x800 pixels
                        </FormText>
                    </FormGroup>
                    <FormGroup>
                        <Label for="mobileFIle">
                            Mobile Image
                        </Label>
                        <InputGroup>
                            <CustomInput
                                type="file"
                                id="mobileFile"
                                name="mobileFile"
                                onChange={(event) => {
                                    if (event != null && event.currentTarget != null && event.currentTarget.files) {
                                        formik.setFieldValue("mobileFile", (event.currentTarget as any).files[0], false)
                                    }
                                }} />
                            {formik.values.mobileImageUrl ? <InputGroupAddon addonType="append" onClick={() => openFile(formik.values.mobileImageUrl)}>Show</InputGroupAddon> : null}
                        </InputGroup>
                        <FormText color="muted">
                            Maximum 1280x1920 pixels
                        </FormText>
                    </FormGroup>
                    <FormGroup>
                        <Label for="buttonText">
                            Url
                        </Label>
                        <input onChange={formik.handleChange} className="form-control" type="text" placeholder="" id="externalUrl" name="externalUrl" value={formik.values.externalUrl || ""} />
                    </FormGroup>
                </ModalBody>
                <ModalFooter>
                    <Button color="secondary" outline onClick={() => props.onClose()}>
                        Cancel
                    </Button>
                    <Button color="primary" type="submit" onClick={formik.handleSubmit}>
                        Save
                    </Button>
                </ModalFooter>
            </Modal>
        </form>);
}