import React from "react";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { ContextMenuTrigger } from "react-contextmenu";
import { Card, NavLink, Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from "reactstrap";
import { Colxx } from "../../../../components/common/CustomBootstrap";
import { ICategory } from "../../categories/category";
import classnames from "classnames";

interface ICategoryListItemProps {
    category: ICategory;
    onCollapse: (id) => void;
    onDelete: (product: ICategory) => void;
    onEdit: (product: ICategory) => void;
    onChangeVisible: (category: ICategory, visible: boolean) => void;
    isCollapsed: boolean
}

export class CategoryItem extends React.Component<ICategoryListItemProps, any> {
    state = {
        isImage: true,
        isCollapsed: false
    }

    onImageError = () => {
        this.setState({ isImage: false });
    }

    render() {
        return (<Colxx xxs="12" className="productList" >
            <Card className={"d-flex flex-row productList__card"}>
                < NavLink className="d-flex">
                    <img alt={this.props.category.Name} src={this.state.isImage ? (this.props.category.FilePath || "") : "https://via.placeholder.com/400?text=No%20image"} className="list-thumbnail responsive border-0 card-img-left" onError={() => this.onImageError()} />
                </NavLink>
                <div className="pl-2 d-flex flex-grow-1 min-width-zero">
                    <div className="card-body align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero align-items-lg-center">
                        <NavLink to={`?p=1`} className="w-30" onClick={() => this.props.onCollapse(this.props.category.Id)}>
                            <div style={{ cursor: "pointer" }} className="list-item-heading mb-1 truncate d-flex align-items-center">
                                {this.props.category.Name}

                                <div className="d-inline-block ml-3" style={{ width: "20px", }}>
                                    <div className={classnames("glyph-icon", { "simple-icon-arrow-left": this.props.isCollapsed, 'simple-icon-arrow-down': !this.props.isCollapsed })} />
                                </div>
                            </div>
                        </NavLink>
                        <NavLink to={`?p=1`} className="w-15">
                            <p className="list-item-heading mb-1 truncate">
                                {this.props.category.Name}
                            </p>
                        </NavLink>
                        <p className="mb-1 text-muted text-small w-50">{this.props.category.Description}</p>
                        <p className="mb-1  w-10 text-right">
                            <Badge color={this.props.category.IsHidden ? "danger" : "light"} pill>
                                {this.props.category.IsHidden ? "Hidden" : "Visible"}
                            </Badge>
                        </p>


                    </div>
                </div>
            </Card>
        </Colxx >)
    }
}

export default CategoryItem;