import React from "react";
import { Colxx } from "../../../components/common/CustomBootstrap";
import { Card, CardImg, CardBody, Row, CardSubtitle, Badge, CardText, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from "reactstrap";
import { NavLink } from "react-router-dom"
import { ContextMenu, MenuItem, ContextMenuTrigger } from 'react-contextmenu';
import { connect } from 'react-redux';

import "./event.scss";
import { EventClient, IEvent } from "../../../api/eventsClient";
import { IAuthUserState } from "../../../redux/auth/authUserState";

export interface ISubfolderListItemViewProps {
    data: any,
    onDelete: () => void,
    onEdit: () => void,
    eventId: number
}

type IProps = ISubfolderListItemViewProps & IAuthUserState

export class SubfolderListItemView extends React.Component<IProps, any> {

    eventClient: EventClient;

    state = {
        isImage: true,
        data: {}
    }

    constructor(props: IProps) {
        super(props);
        this.eventClient = new EventClient();
    }

    onImageError = () => {
        this.setState({ isImage: false })
    }

    onEdit = (e) => {
        e.preventDefault();
        this.props.onEdit();
    }

    onDelete = (e) => {
        e.preventDefault();
        this.props.onDelete();
    }

    render() {
        return (<Colxx sm="6" lg="4" xl="3" className="mb-3 eventListItem" key={this.props.data.FolderId}>
            <ContextMenuTrigger id="menu_id" collect={(props) => { return { data: props.data, folderId: this.props.data.FolderId } }}>
                <NavLink to={`/photo-hosting/event/${this.props.eventId}/${this.props.data.FolderId}`} className="w-40 w-sm-100">
                    <Card onClick={() => { }}>
                        <div className="position-relative">
                            <CardImg top alt={this.props.data.Name} src={this.state.isImage ? this.props.data.ThumbSrc : "https://via.placeholder.com/600?text=No%20image"} onError={(e => this.onImageError())} />

                            {
                                this.props.data.Password && <Badge color="danger" pill className="position-absolute badge-top-left">PASSWORD</Badge>
                            }

                            <div className="eventListItem__menu">
                                <UncontrolledDropdown>
                                    <DropdownToggle size="xs" color="primary" onClickCapture={e => e.preventDefault()}>
                                        <i className="simple-icon-menu" />
                                    </DropdownToggle>
                                    <DropdownMenu>
                                        <DropdownItem onClick={(e) => this.onEdit(e)}>
                                            <i className="simple-icon-docs" /> <span>Settings</span>
                                        </DropdownItem>
                                        <DropdownItem onClick={(e) => this.onDelete(e)}>
                                            <i className="simple-icon-trash" /> <span>Delete</span>
                                        </DropdownItem>
                                    </DropdownMenu>
                                </UncontrolledDropdown>
                            </div>
                        </div>
                        <CardBody>
                            <CardSubtitle className="mb-4">
                                {this.props.data.Name}
                            </CardSubtitle>
                        </CardBody>

                    </Card>
                </NavLink>
            </ContextMenuTrigger>

        </Colxx >)
    }
}

const mapStateToProps = ({ authUser }) => {
    return authUser;
};


export default connect(mapStateToProps)(SubfolderListItemView);
