import React from "react";
import { Card, CardBody, CustomInput, Form, FormGroup, FormText, InputGroup, InputGroupAddon, InputGroupText, Label, Row } from "reactstrap";
import { Colxx } from "../../../components/common/CustomBootstrap";
import TopNavigation from "../../../components/wizard/TopNavigation";
import { Wizard, Steps, Step } from 'react-albus';
import BottomNavigation from "../../../components/wizard/BottomNavigation";
import { PhotoClient } from "../../../api/photoClient";
import { CategoryClient } from "../../../api/categoriesClient";
import { AttributesClient } from "../../../api/attributesClient";
import { responseItemToAttributeGroup, responseItemToCategory } from "../PhotoOrderHelper";
import AttributeSelector, { IAttributeGroupItem } from "../products/components/AttributesSelector";
import { NotificationManager } from "../../../components/common/react-notifications";
import { Formik, Form as FormikForm, Field } from "formik";
import "./WizardPage.scss";
import PriceRangeSelector from "../products/components/PriceRangeSelector";
import Select from 'react-select'
import { Link } from "react-router-dom";
import { TemplateSelector } from "../products/components/TemplateSelector";
import WizardStandardProduct from "./WizardStandard";
import Wizard3D from "./Wizard3D";
import { ProductPriceDTO, ProductsClient, ProductsTemplateDTO } from "../../../api/ApiClient";
import { BASE_URL } from "../../../api/settings";
import { API_BASE } from "../../../api/ApiSettings";
import _ from "underscore";

enum WizardType { Standard, ThreeD }

export class ProductWizard extends React.Component<any, any> {

    photoClient: PhotoClient = new PhotoClient();

    productClient: ProductsClient = new ProductsClient(API_BASE);

    categoriesClient: CategoryClient = new CategoryClient();
    attributesClient: AttributesClient = new AttributesClient();


    constructor(props) {
        super(props);

        this.state = {
            customSize: false,
            categories: [],
            attributeGroups: [],
            saving: false,
            saved: false,
            wizardType: null
        }

        this.categoriesClient.List("ProductCategories").then(response => {
            let data = response.data.Data.map(x => responseItemToCategory(x))
            this.setState({ categories: data })
        });

        this.attributesClient.List("ProductAttributes").then(response => {
            let data = response.data.Data.map(x => responseItemToAttributeGroup(x))
            this.setState({ attributeGroups: data })
        });
    }

    initialValues = {
        Name: "",
        Size: 0,
        Description: "",
        Price: null,
        SizeSelect: 0,
        File: null,
        attributeGroups: [],
        categories: [],
        QuantityRangeMin: "",
        QuantityRangeMax: "",
        PriceRange: [],
        ProductType: 0,
        ProductTemplates: [],
        Model3D: null,
    }

    topNavClick = (stepItem, push) => {
        if (this.blockInterface) {
            return;
        }
        push(stepItem.id);
    }

    onClickNext = (goToNext, steps, step) => {
        step.isDone = true;
        if (steps.length - 1 <= steps.indexOf(step)) {
            return;
        }
        goToNext();
    };

    onClickPrev = (goToPrev, steps, step) => {
        if (steps.indexOf(step) <= 0) {
            return;
        }
        goToPrev();
    };

    onSubmit = async (values, formik) => {
        if (this.state.wizardType == WizardType.Standard) {
            let formData = new FormData();
            formData.append("Description", values.Description);
            formData.append("Name", values.Name);
            formData.append("Price", "0");
            formData.append("Size", values.Size);
            formData.append("FileAsBase64", values.File);
            formData.append("QuantityRangeMin", values.QuantityRangeMin);
            formData.append("QuantityRangeMax", values.QuantityRangeMax);
            formData.append("ProductType", values.ProductType);

            values.PriceRange.forEach((x, index) => {
                formData.append(`ProductPrices[${index}].PriceLevelFrom`, x.from);
                if (x.to) {
                    formData.append(`ProductPrices[${index}].PriceLevelTo`, x.to);
                }

                formData.append(`ProductPrices[${index}].Price`, x.value);
                formData.append(`ProductPrices[${index}].IsDeleted`, "false");
            });

            values.categories.forEach(x => {
                formData.append("CategoriesIds", x.toString());
            });

            let attributesGroups: IAttributeGroupItem[] = values.attributeGroups;
            Array.prototype.concat.apply([], attributesGroups.map(x => x.attributes)).forEach((attrId: number) => {
                if (!isNaN(attrId)) {
                    formData.append("AttributesIds", attrId.toString());
                }
            });

            this.setState({ saving: true })
            this.photoClient.AddProduct(formData).then((response) => {
                if (response.data.Error) {
                    this.setState({ saving: false })
                } else {
                    this.setState({ saved: true, saving: false })
                }
                formik.setSubmitting(false);
            });
        } else {
            // var data = new Create3DProduct({
            //     imageFile: { data: values.File, fileName: values.File.name },
            //     layerImageFile: { data: values.layerImageFile, fileName: values.layerImageFile.name },
            //     attributesIds: values.attributeGroups.map(x => x.id), //TO FIX
            //     categoriesIds: values.categories, //TO FIX
            //     description: values.Description,
            //     name: values.Name,
            //     productPrices: values.PriceRange.map(x => { return new ProductPriceDTO({ price: x.value, priceLevelFrom: x.from, priceLevelTo: x.to, id: 0 }) }),
            //     sizes: values.ProductTemplates.map(x => { return new ProductsTemplateDTO({ id: 0, productId: 0, positionX: x.positionX, positionY: x.positionY, sizeX: x.sizeX, sizeY: x.sizeY }) }),
            //     model3D: values.Model3D.id
            // })
            // var data = {
            //     attributesIds: values.attributeGroups.map(x => x.id), //TO FIX
            //     categoriesIds: values.categories, //TO FIX
            //     description: values.Description,
            //     imageFile: { data: values.File, fileName: values.File.name },
            //     layerImageFile: { data: values.layerImageFile, fileName: values.layerImageFile.name },
            //     name: values.Name,
            //     productPrices: values.PriceRange.map(x => { return new ProductPriceDTO({ price: x.value, priceLevelFrom: x.from, priceLevelTo: x.to, id: 0 }) }),
            //     sizes: values.ProductTemplates.map(x => { return new ProductsTemplateDTO({ id: 0, productId: 0, positionX: x.positionX, positionY: x.positionY, sizeX: x.sizeX, sizeY: x.sizeY }) }),
            //     model3D: values.Model3D.id
            // };

            var data: any = {};
            data.attributesIds = values.attributeGroups.map(x => x.id); //TO FIX
            data.categoriesIds = values.categories; //TO FIX
            data.description = values.Description;
            data.imageFile = { data: values.File, fileName: values.File.name };
            data.layerImageFile = { data: values.layerImageFile, fileName: values.layerImageFile.name };
            data.name = values.Name;
            data.productPrices = values.PriceRange.map(x => { return new ProductPriceDTO({ price: x.value, priceLevelFrom: x.from, priceLevelTo: x.to, id: 0 }).toString() });
            data.sizes = values.ProductTemplates.map(x => { return new ProductsTemplateDTO({ id: 0, productId: 0, positionX: x.positionX, positionY: x.positionY, sizeX: x.sizeX, sizeY: x.sizeY }).toString() })
            data.model3D = values.Model3D.id;

            this.setState({ saving: true })
            await this.productClient.add3DProduct(data.name, data.description, data.imageFile, data.layerImageFile, data.sizes, [], data.productPrices, [], data.categoriesIds, data.attributesIds, data.model3D);
            this.setState({ saved: true, saving: false });
            formik.setSubmitting(false);

        }

    }

    get categories() {
        return this.state.categories.map(category => ({ value: category.Id, label: category.Name }));
    }

    get attributeGroups() {
        let data: any[] = [];
        this.state.attributeGroups.forEach(x => {
            data.push({
                label: x.Name,
                options: x.attributes.map(attr => ({ label: attr.Name, value: attr.Id }))
            })
        });
        return data;
    }

    get blockInterface() {
        return this.state.saved || this.state.saving
    }

    render() {
        return (
            <Formik
                initialValues={this.initialValues}
                onSubmit={this.onSubmit}
                enableReinitialize={true}
            >
                {({
                    handleSubmit,
                    setFieldValue,
                    setFieldTouched,
                    handleChange,
                    handleBlur,
                    values,
                    errors,
                    touched,
                    isSubmitting,
                    submitForm
                }) => (
                    <FormikForm>
                        <Card>
                            <CardBody className="wizard wizard-default">
                                <h1 style={{ marginTop: 18 }}>Product wizard</h1>
                                {this.state.wizardType == null && <>
                                    <Row>
                                        <Colxx className="text-center">
                                            <h1>Select product that you will create</h1>
                                        </Colxx>
                                    </Row>
                                    <Row>
                                        <Colxx className={"text-right"}>
                                            <button onClick={() => this.setState({ wizardType: WizardType.Standard })} className="btn btn-primary btn-lg">
                                                Standard product
                                            </button>
                                        </Colxx>
                                        <Colxx className={"text-left"}>
                                            <button onClick={() => this.setState({ wizardType: WizardType.ThreeD })} className="btn btn-primary btn-lg">
                                                3D product
                                            </button>
                                        </Colxx>
                                    </Row>
                                </>}
                                {this.state.wizardType == WizardType.Standard && <>
                                    <WizardStandardProduct {...this.state} setFieldValue={setFieldValue} values={values} handleChange={handleChange} handleBlur={handleBlur} submitForm={submitForm} categories={this.categories} blockInterface={this.blockInterface} />
                                </>}

                                {this.state.wizardType == WizardType.ThreeD && <>
                                    <Wizard3D {...this.state} setFieldValue={setFieldValue} values={values} handleChange={handleChange} handleBlur={handleBlur} submitForm={submitForm} categories={this.categories} blockInterface={this.blockInterface} />
                                </>}

                            </CardBody>
                        </Card>
                    </FormikForm>)
                }
            </Formik>)
    }

}

export default ProductWizard;