import React from "react";
import { ContextMenuTrigger } from "react-contextmenu";
import { NavLink, Card, UncontrolledDropdown, DropdownToggle, DropdownItem, DropdownMenu, Badge } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faBars } from "@fortawesome/free-solid-svg-icons"
import { Draggable } from "react-beautiful-dnd";
import { AdvertisementDTO } from "../../../../api/ApiClient";
import { Colxx } from "../../../../components/common/CustomBootstrap";


interface IAdvertisementListItem {
    entity: AdvertisementDTO;
    onCheckItem: (e, id) => void;
    onDelete: (entity: AdvertisementDTO) => void;
    onEdit: (entity: AdvertisementDTO) => void;
    onChangeVisible: (entity: AdvertisementDTO, visible: boolean) => void;
    index: number
}

export class AdvertisementListItem extends React.Component<IAdvertisementListItem, any> {

    state = {
        isImage: true
    }

    onImageError = () => {
        console.log('err');
        this.setState({ isImage: false });
    }

    render() {
        return (
            <Draggable key={this.props.entity.id} draggableId={"category-" + this.props.entity.id} index={this.props.index}>
                {(provided, snapshot) => (
                    <div ref={provided.innerRef} {...provided.draggableProps} key={this.props.entity.id}>
                        <Colxx xxs="12" className="mb-3 productList" >
                            <ContextMenuTrigger id="product_menu" collect={(props) => { return { data: props.data, product: this.props.entity } }}>
                                <Card
                                    {...provided.dragHandleProps}
                                    onClick={(event) => this.props.onCheckItem(event, this.props.entity.id)}
                                    className={"d-flex flex-row productList__card"}>
                                    < NavLink to={`?p=${this.props.entity.id}`} className="d-flex">
                                        <img src={this.state.isImage ? (this.props.entity.imageUrl || "") : "https://via.placeholder.com/400?text=No%20image"} className="list-thumbnail responsive border-0 card-img-left" onError={() => this.onImageError()} />
                                    </NavLink>
                                    <div className="pl-2 d-flex flex-grow-1 min-width-zero">
                                        <div className="card-body align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero align-items-lg-center">
                                            <NavLink to={`?p=1`} className="w-15">
                                                <p className="list-item-heading mb-1 truncate">
                                                    {this.props.entity.buttonText}
                                                </p>
                                            </NavLink>
                                            <p className="mb-1 text-muted text-small w-50">{this.props.entity.externalUrl}</p>
                                            <p className="mb-1  w-10 text-right">
                                                <Badge color={this.props.entity.isHidden ? "danger" : "light"} pill>
                                                    {this.props.entity.isHidden ? "Hidden" : "Visible"}
                                                </Badge>
                                            </p>
                                            <div className="mb-1 w-25 text-right pr-3">
                                                <UncontrolledDropdown className="productList__dropdown">
                                                    <DropdownToggle caret color="secondary" outline>
                                                        Actions
                                                    </DropdownToggle>
                                                    <DropdownMenu>
                                                        <DropdownItem onClick={() => this.props.onEdit(this.props.entity)}>
                                                            <i className="simple-icon-docs" /> <span>Edit</span>
                                                        </DropdownItem>
                                                        <DropdownItem onClick={() => this.props.onChangeVisible(this.props.entity, this.props.entity.isHidden ? true : false)}>
                                                            <i className="simple-icon-docs" /> <span>Set {this.props.entity.isHidden ? "Visible" : "Hidden"}</span>
                                                        </DropdownItem>
                                                        <DropdownItem onClick={() => this.props.onDelete(this.props.entity)}>
                                                            <i className="simple-icon-trash" /> <span>Delete</span>
                                                        </DropdownItem>
                                                    </DropdownMenu>
                                                </UncontrolledDropdown>
                                            </div>
                                            <div  >
                                                <FontAwesomeIcon icon={faBars} />
                                            </div>
                                        </div>
                                    </div>
                                </Card>
                            </ContextMenuTrigger>
                        </Colxx>
                    </div>
                )}
            </Draggable>
        );
    }
}

export default AdvertisementListItem;