import React from "react";
import { ContextMenuTrigger } from "react-contextmenu";
import { Badge, Card, Collapse, DropdownItem, DropdownMenu, DropdownToggle, NavLink, Row, UncontrolledDropdown } from "reactstrap";
import { Colxx } from "../../../../components/common/CustomBootstrap";
import { IProduct } from "../product";
import classnames from "classnames";
import { ICategory } from "../../categories/category";
import { IAttribute, IAttributeGroup } from "../../attributes/attribute";
import QRCode from 'qrcode.react';
import "./ProductListItem.scss";
import { Draggable } from "react-beautiful-dnd";
import { WEB_ORDERING_BASE_URL } from "../../../../api/settings";
import { AuthService } from "../../../../services/auth.service";

interface IThumbListView {
    product: IProduct;
    onDelete: (product: IProduct) => void;
    onEdit: (product: IProduct) => void;
    categories: ICategory[],
    attributesGroups: IAttributeGroup[],
    onChangeVisible: (product: IProduct, visible: boolean) => void;
    onCollapse: (id) => void;
    isCollapsed: boolean;
}

interface IProductListItemState {
    isImage: boolean,
}



export class SubProductItem extends React.Component<IThumbListView, IProductListItemState> {

    authService: AuthService = new AuthService();

    state = {
        isImage: true
    }

    onImageError = () => {
        this.setState({ isImage: false });
    }

    getCategory = (categoryId: number): ICategory | undefined => {
        if (this.props.categories) {
            return this.props.categories.find(x => x.Id == categoryId);
        }
        return undefined;
    }

    getAttribute = (attributeId: number): IAttribute | undefined => {
        if (this.props.attributesGroups) {
            var attributes: IAttribute[] = Array.prototype.concat.apply([], this.props.attributesGroups.map(x => x.attributes))
            return attributes.find(x => x.Id == attributeId);
        }
        return undefined;
    }

    categoryListName = (categoriesIds: number[]) => {
        var categories: ICategory[] | undefined = [];
        categoriesIds.forEach(c => {
            var asd = this.getCategory(c);
            if (asd) {
                categories?.push(asd);
            }
        });
        return categories.map(x => x.Name).join(", ")
    }

    attributeListName = (categoriesIds: number[]) => {
        var categories: IAttribute[] | undefined = [];
        categoriesIds.forEach(c => {
            var asd = this.getAttribute(c);
            if (asd) {
                categories?.push(asd);
            }
        });
        return categories.map(x => x.Name).join(", ")
    }

    onDownloadQrCode = (product: IProduct) => {
        const canvas: any = document.getElementById(`qrcodedownload-${this.props.product.Id}`);
        const pngUrl = canvas.toDataURL("image/png").replace("image/png", "image/octet-stream");
        let downloadLink = document.createElement("a");
        downloadLink.href = pngUrl;
        downloadLink.download = `qrcode-${product.Id}.png`;
        document.body.appendChild(downloadLink);
        downloadLink.click();
        document.body.removeChild(downloadLink);
    }

    qrCodeValue = () => {
        if (this.props.product.CategoriesIds.length) {
            return `${WEB_ORDERING_BASE_URL}/photographer/${this.authService.getId()}/categories/${this.props.product.CategoriesIds[0]}/${this.props.product.MainProductId}`
        }

        return "";
    }

    render() {
        return (
            <Colxx xxs="12" key={this.props.product.Id} className="productList" >
                <Card style={{ marginLeft: "3rem" }} key={"subprod-" + this.props.product.Id} className={"d-flex flex-row productList__card"}>
                    <NavLink to={`?p=${this.props.product.Id}`} className="d-flex align-items-center">
                        <img alt={this.props.product.Name} src={this.state.isImage ? this.props.product.FileUrl : "https://via.placeholder.com/400?text=No%20image"} className="list-thumbnail responsive border-0 card-img-left" onError={() => this.onImageError()} />
                    </NavLink>
                    <div className="pl-2 d-flex flex-grow-1 min-width-zero">
                        <div className="card-body align-self-center d-flex flex-column flex-lg-row min-width-zero align-items-lg-center">
                            <NavLink to={`?p=1`} className="w-30 w-sm-100" style={{ marginRight: 25 }}>
                                <p className="list-item-heading mb-1 truncate">
                                    {this.props.product.Name} <span className="text-muted text-small ml-2">({this.props.product.Id}-{this.props.product.Id})</span>
                                </p>
                            </NavLink>
                            <p className="mb-1 text-muted text-small w-40">{this.props.product.Description}</p>
                            <div className="mb-1 text-muted text-small w-30 w-sm-100">
                                <Row>
                                    <Colxx>
                                        <div>
                                            Category: <strong>{this.categoryListName(this.props.product.CategoriesIds)}</strong>
                                        </div>
                                        <div>
                                            Attributes: <strong>{this.attributeListName(this.props.product.AttributesIds)}</strong>
                                        </div>
                                        <div className="d-flex align-items-center">
                                            Is hidden:
                                            <Badge color={this.props.product.IsHidden ? "danger" : "light"} pill>
                                                {this.props.product.IsHidden ? "Yes" : "No"}
                                            </Badge>
                                        </div>
                                    </Colxx>

                                </Row>
                            </div>
                            <div className="d-flex justify-content-end align-items-center w-15">
                                <div className="qrCode" style={{ display: "none" }}>\
                                    <QRCode
                                        id={`qrcodedownload-${this.props.product.Id}`}
                                        value={this.qrCodeValue()}
                                        size={400}
                                        level={"H"}
                                        includeMargin={true}
                                    />
                                </div>
                                <div>
                                    <UncontrolledDropdown className="productList__dropdown">
                                        <DropdownToggle caret color="secondary" outline>
                                            Actions
                                        </DropdownToggle>
                                        <DropdownMenu>
                                            {this.qrCodeValue() != "" && <DropdownItem onClick={() => this.onDownloadQrCode(this.props.product)}>
                                                <i className="glyph-icon iconsminds-qr-code" /> <span>Download QR Code</span>
                                            </DropdownItem>}
                                            <DropdownItem onClick={() => this.props.onEdit(this.props.product)}>
                                                <i className="simple-icon-docs" /> <span>Edit</span>
                                            </DropdownItem>
                                            <DropdownItem onClick={() => this.props.onChangeVisible(this.props.product, this.props.product.IsHidden ? true : false)}>
                                                <i className="simple-icon-trash" /> <span>Set {this.props.product.IsHidden ? "Visible" : "Hidden"}</span>
                                            </DropdownItem>
                                            <DropdownItem onClick={() => this.props.onDelete(this.props.product)}>
                                                <i className="simple-icon-trash" /> <span>Delete</span>
                                            </DropdownItem>


                                        </DropdownMenu>
                                    </UncontrolledDropdown>
                                </div>
                                <a style={{ width: "20px" }} href="#"> </a>
                            </div>
                        </div>
                    </div>
                </Card>
            </Colxx>
        );
    }
}

export default SubProductItem;