import React, { useEffect, useState } from 'react';
import { DragDropContext, Droppable, DropResult } from 'react-beautiful-dnd';
import { Button, Card, CardBody, CardTitle, Row } from 'reactstrap';
import { AdvertisementClient, AdvertisementDTO } from '../../../../api/ApiClient';
import { API_BASE } from '../../../../api/ApiSettings';
import { Colxx, Separator } from '../../../../components/common/CustomBootstrap';
import { NotificationManager } from '../../../../components/common/react-notifications';
import { AdvertisementForm } from './advertisementForm';
import AdvertisementListItem from './advertisementListItem';

export function AdvertisementsList() {
    const advertisementClient = new AdvertisementClient(API_BASE);
    const [advertisements, setAdvertisements] = useState<AdvertisementDTO[]>([])
    const [editAdvertisement, setEditAdvertisement] = useState<AdvertisementDTO | null>(null);

    const populateAdvertisements = async () => {
        setAdvertisements(await advertisementClient.list())
    }

    const reorder = (list: AdvertisementDTO[], startIndex: number, endIndex: number): AdvertisementDTO[] => {
        const result = Array.from(list);
        const [removed] = result.splice(startIndex, 1);
        result.splice(endIndex, 0, removed);

        return result;
    };

    useEffect(() => {
        populateAdvertisements().catch(console.error);
    }, [])

    function onAdd() {
        console.log()
        setEditAdvertisement(new AdvertisementDTO());
    }

    async function onCloseForm() {
        await populateAdvertisements()
        setEditAdvertisement(null);
    }

    async function onDragEnd(result: DropResult) {
        if (result.source && result.destination) {
            const items = reorder(advertisements, result.source.index, result.destination.index);
            setAdvertisements(items);
            let ids = items.map(x => x.id);
            await advertisementClient.reorder(ids);
            await populateAdvertisements();
        }
    }

    async function onSetVisible(entity: AdvertisementDTO, visible: boolean) {
        entity.isHidden = !visible;
        NotificationManager.info("Updating visible.", 'Updating...', 3000, null, null, '');
        await advertisementClient.update(entity);
        NotificationManager.success("Updated.", 'Success', 3000, null, null, '');
        await populateAdvertisements();
    }

    async function onDelete(entity: AdvertisementDTO) {
        NotificationManager.info("Deleting banner", 'Deleting...', 3000, null, null, '');
        await advertisementClient.delete(entity.id);
        NotificationManager.success("Deleted.", 'Success', 3000, null, null, '');
        await populateAdvertisements();
    }

    async function onEdit(entity: AdvertisementDTO) {
        setEditAdvertisement(entity);
    }

    return (<>
        <Card>
            <CardBody>
                <CardTitle>
                    Banner settings
                    <div className="text-zero top-right-button-container">
                        <Button
                            color="primary"
                            size="lg"
                            className="top-right-button"
                            onClick={onAdd}
                        >
                            <i className="simple-icon-plus" /> ADD BANNER
                        </Button>
                        {'  '}
                    </div>
                </CardTitle>
                <Row>
                    <Colxx xxs="12">
                        <DragDropContext onDragEnd={onDragEnd}>
                            <Droppable droppableId="categories">
                                {(provided, snapshot) => (
                                    <div {...provided.droppableProps} ref={provided.innerRef}>
                                        {advertisements.map((p, index) => {
                                            return (
                                                <AdvertisementListItem onChangeVisible={onSetVisible} key={"advitem" + index} entity={p} onDelete={onDelete} onEdit={onEdit} onCheckItem={() => { }} index={index} />
                                            )
                                        })}
                                        {provided.placeholder}
                                    </div>
                                )}
                            </Droppable>

                        </DragDropContext>
                    </Colxx>
                </Row>
            </CardBody>
        </Card>
        {editAdvertisement != null ? <AdvertisementForm advertisement={editAdvertisement} onClose={onCloseForm} /> : null}
    </>)
}

