import React from 'react';
import { v4 as uuidv4 } from 'uuid';
import {
    CustomInput,
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Input,
    Label,
    FormGroup,
    InputGroup,
    InputGroupAddon,
    FormText,
} from 'reactstrap';
import Switch from 'rc-switch';
import 'rc-switch/assets/index.css';
import { Formik, Form, Field } from 'formik';
import { IAddEditFolderDialogViewModel, EventClient } from '../../../../api/eventsClient';
import { NotificationManager } from '../../../../components/common/react-notifications';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSpinner } from '@fortawesome/free-solid-svg-icons';
export interface IAddNewSubfolderProps {
    modalOpen: boolean,
    toggleModal: () => void,
    onCreate: () => void,
    eventId: number
}

export class AddNewSubfolder extends React.Component<IAddNewSubfolderProps, any> {

    eventClient: EventClient = new EventClient();

    initialValues: IAddEditFolderDialogViewModel | any = {
        Id: 0,
        EventId: 0,
        Name: "",
        Password: "",
        FolderCoverPhoto: null,
        IsPasswordProtected: false
    }

    constructor(props) {
        super(props);

        this.initialValues.EventId = this.props.eventId;

        this.state = {

        }
    }

    onSubmit = (values, formik) => {
        this.eventClient.SaveFolder(values).then((response) => {
            if (response.data.Error) {
                NotificationManager.error("Saving error", response.data.Error, 3000, null, null, '');
            } else {
                NotificationManager.success("Data saved", "Data saved sucessfully.", 3000, null, null, '');
                this.props.onCreate();
                this.props.toggleModal();
            }
            formik.setSubmitting(false);
        });
    }

    render() {
        return (<Formik
            initialValues={this.initialValues}
            onSubmit={this.onSubmit}
            enableReinitialize={true}
            validate={() => { }}
        >
            {({
                handleSubmit,
                setFieldValue,
                setFieldTouched,
                handleChange,
                handleBlur,
                values,
                errors,
                touched,
                isSubmitting,
                submitForm
            }) => (
                <Form><Modal
                    isOpen={this.props.modalOpen}
                    toggle={this.props.toggleModal}
                    wrapClassName="modal-right"
                    backdrop="static"
                >
                    <ModalHeader toggle={this.props.toggleModal}>
                        Add new subfolder
                    </ModalHeader>
                    <ModalBody>
                        <FormGroup>
                            <Label for="name">
                                Name
                            </Label>
                            <Field className="form-control" name="Name" type="text" id="Name" placeholder={"Subfolder name"} maxLength={30} />
                            <FormText color="muted">
                                Maximum 30 letters
                            </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label for="file">
                                Cover
                            </Label>
                            <InputGroup>
                                <CustomInput
                                    type="file"
                                    id="FolderCoverPhoto"
                                    name="FolderCoverPhoto"
                                    onChange={(event) => {
                                        if (event != null && event.currentTarget != null && event.currentTarget.files) {
                                            setFieldValue("FolderCoverPhoto", event.currentTarget.files[0]);
                                        }
                                    }}
                                />
                            </InputGroup>
                            <FormText color="muted">
                                Maximum 1920x1280 pixels
                            </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label>
                                Is password protected?
                            </Label>
                            <Switch
                                loadingIcon={null}
                                className="custom-switch custom-switch-primary custom-switch-small"
                                checked={values.IsPasswordProtected}
                                onChange={(isPasswordProtected) => { setFieldValue("IsPasswordProtected", isPasswordProtected); if (!isPasswordProtected) { setFieldValue("Password", "") } }}
                            />
                        </FormGroup>
                        {
                            values.IsPasswordProtected && <FormGroup>
                                <Label for="password">
                                    Password
                                </Label>
                                <Field className="form-control" name="Password" type="text" id="Password" placeholder={"Password"} />
                            </FormGroup>
                        }
                    </ModalBody>
                    <ModalFooter>
                        <Button color="secondary" outline onClick={this.props.toggleModal}>
                            Cancel
                        </Button>
                        <Button disabled={isSubmitting} color="primary" type="submit" onClick={() => submitForm()}>
                            {isSubmitting && <FontAwesomeIcon size="lg" icon={faSpinner} spin={true} style={{ color: "#fff" }} />} Submit
                        </Button>
                    </ModalFooter>
                </Modal>
                </Form>
            )}</Formik>
        );
    }
}

export default AddNewSubfolder;
