import React from "react";
import { Row, Button } from "reactstrap";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb"
import { DeleteModal } from "../../../components/modals/delete/DeleteModal";
import { AttributeListItem } from "./attributeListItem";
import { IAttributeGroup, IAttribute, ICreateAttribute, ICreateAttributeGroup } from "./attribute";
import AddEditAttributeGroupModal from "./addEditAttributeGroupModal";
import AddEditAttributeModal from "./addEditAttributeModal";
import { DragDropContext, Draggable, Droppable, DropResult } from "react-beautiful-dnd";
import { AttributesClient } from "../../../api/attributesClient";
import NotificationManager from "../../../components/common/react-notifications/NotificationManager";

export interface IAttributesState {
    attributesGroups: IAttributeGroup[],
    editedAttributeGroup: IAttributeGroup | null,
    deleteAttributeGroup: IAttributeGroup | null,
    editAttribute: IAttribute | null,
    deleteAttribute: IAttribute | null,
    loading: boolean
}

const reorder = (list: any[], startIndex: number, endIndex: number): any[] => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
};

export class Attributes extends React.Component<any, IAttributesState> {

    //private static Key: string = "ProductAttributes"

    attributesClient: AttributesClient = new AttributesClient();

    constructor(props) {
        super(props);

        this.state = {
            attributesGroups: [],
            editedAttributeGroup: null,
            deleteAttributeGroup: null,
            deleteAttribute: null,
            editAttribute: null,
            loading: true
        }
    }

    responseAttributeItemToAttribute = (item: any, attributeGroupId: number): IAttribute => {
        return {
            Id: item.id,
            Name: item.name,
            Description: item.description,
            PriceCorrectionPercent: item.priceCorrectionPercent,
            PriceCorrectionCurrent: item.priceCorrectionCurrent,
            Position: item.position,
            AttributeGroupId: attributeGroupId
        }
    }

    responseItemToAttributeGroup = (item: any): IAttributeGroup => {
        let i = {
            Id: item.id,
            Name: item.name,
            Key: item.key,
            attributes: [],
            Position: item.position
        };

        (item.attributes as any[]).forEach(attr => {
            (i.attributes as any[]).push(this.responseAttributeItemToAttribute(attr, i.Id));
        });

        return i;
    }

    componentDidUpdate(prevProps) {
        if (this.props.dataKey != prevProps.dataKey) {
            this.setState({
                attributesGroups: [],
                editedAttributeGroup: null,
                deleteAttributeGroup: null,
                deleteAttribute: null,
                editAttribute: null
            }, () => {
                this.componentDidMount();
            });
        }
    }

    componentDidMount() {
        this.setState({ loading: true })
        this.attributesClient.List(this.props.dataKey).then(response => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'List attribute error', 3000, null, null, '');
                return;
            }
            console.log(response);
            var attributesGroup: IAttributeGroup[] = [];
            response.data.Data.forEach(item => {
                attributesGroup.push(this.responseItemToAttributeGroup(item));
            });
            this.setState({ attributesGroups: attributesGroup });
            this.setState({ loading: false })
        });
    }


    edit = (attributeGroup: IAttributeGroup) => {
        let attributesGroups = [...this.state.attributesGroups];
        let c = attributesGroups.find(x => x.Id == attributeGroup.Id);
        c = Object.assign(c, attributeGroup);
        NotificationManager.info("Saving attribute group", 'Saving...', 3000, null, null, '');
        this.attributesClient.Update(this.attributeGroupToCreateAttributeGroup(c), this.props.dataKey).then(response => {
            NotificationManager.success("Atribute group saved.", 'Saved', 3000, null, null, '');
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Update category error', 3000, null, null, '');
                return;
            }
            this.setState({ attributesGroups: attributesGroups });
        })
    }

    create = (attributeGroup: IAttributeGroup) => {
        let attributesGroups = [...this.state.attributesGroups];
        NotificationManager.info("Saving attribute group", 'Saving...', 3000, null, null, '');
        this.attributesClient.Add(this.attributeGroupToCreateAttributeGroup(attributeGroup), this.props.dataKey).then(response => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Create category error', 3000, null, null, '');
                return;
            }
            NotificationManager.success("Atribute group saved.", 'Saved', 3000, null, null, '');
            var attrGroup = this.responseItemToAttributeGroup(response.data.Data);
            attributesGroups.push(attrGroup);
            this.setState({ attributesGroups: attributesGroups });
        });

    }

    delete = (attributeGroup: IAttributeGroup) => {
        let attributesGroups = [...this.state.attributesGroups];
        NotificationManager.info("Deleting attribute group", 'Deleting...', 3000, null, null, '');
        this.attributesClient.Delete(attributeGroup.Id).then(response => {
            let cindex = attributesGroups.findIndex(x => x.Id == attributeGroup.Id);
            if (cindex > -1) {
                NotificationManager.success("Atribute group deleted.", 'Deleted', 3000, null, null, '');
                attributesGroups.splice(cindex, 1);
                this.setState({ attributesGroups: attributesGroups });
            }
        });
    }

    attributeToCreateAttrbute = (attribute: IAttribute): ICreateAttribute => {
        let data: ICreateAttribute = {
            AttributeGroupId: attribute.AttributeGroupId,
            Description: attribute.Description,
            Id: attribute.Id,
            Name: attribute.Name,
            Position: attribute.Position,
            PriceCorrectionCurrent: attribute.PriceCorrectionCurrent,
            PriceCorrectionPercent: attribute.PriceCorrectionPercent
        }
        return data;
    }

    attributeGroupToCreateAttributeGroup = (group: IAttributeGroup): ICreateAttributeGroup => {
        let data: ICreateAttributeGroup = {
            Id: group.Id,
            Name: group.Name,
            Key: group.Key,
            attributes: group.attributes.map(attr => this.attributeToCreateAttrbute(attr))
        }
        return data;
    }

    createAttribute = (attribute: ICreateAttribute) => {
        let attributesGroups = [...this.state.attributesGroups];
        let attrGroup = attributesGroups.find(attrG => attrG.Id == attribute.AttributeGroupId);
        if (attrGroup) {
            let requestData = this.attributeGroupToCreateAttributeGroup(attrGroup);
            requestData.attributes.push(attribute);
            NotificationManager.info("Saving attribute", 'Saving...', 3000, null, null, '');
            this.attributesClient.Update(requestData, this.props.dataKey).then(response => {
                if (response.data.Error) {
                    NotificationManager.error(response.data.Error, 'Update attribute error', 3000, null, null, '');
                    return;
                }
                NotificationManager.success("Atribute saved.", 'Saved', 3000, null, null, '');
                let responseAttrGr = this.responseItemToAttributeGroup(response.data.Data);
                let attrGroupIndex = attributesGroups.findIndex(x => x.Id == (responseAttrGr as any).Id);
                if (attrGroupIndex > -1) {
                    attributesGroups[attrGroupIndex] = responseAttrGr;
                }
                this.setState({ attributesGroups: attributesGroups });
            });
        }
    }

    editAttribute = (attribute: ICreateAttribute) => {
        let attributesGroups = [...this.state.attributesGroups];
        let attrGroup = attributesGroups.find(attrG => attrG.Id == attribute.AttributeGroupId);
        if (attrGroup) {
            let attr = attrGroup.attributes.find(x => x.Id == attribute.Id);
            if (attr) {
                let requestGroup = this.attributeGroupToCreateAttributeGroup(attrGroup);
                let indexOfAttr = requestGroup.attributes.findIndex(attr => attr.Id == attribute.Id);
                if (indexOfAttr > -1) {
                    requestGroup.attributes[indexOfAttr] = attribute;
                }
                NotificationManager.info("Saving attribute", 'Saving...', 3000, null, null, '');
                this.attributesClient.Update(requestGroup, this.props.dataKey).then(response => {
                    if (response.data.Error) {
                        NotificationManager.error(response.data.Error, 'Update category error', 3000, null, null, '');
                        return;
                    }
                    NotificationManager.success("Attribute saved.", 'Saved', 3000, null, null, '');
                    attrGroup = this.responseItemToAttributeGroup(response.data.Data);
                    var indexOfAttrGroup = attributesGroups.findIndex(x => x.Id == (attrGroup as any).Id);
                    if (indexOfAttrGroup > -1) {
                        attributesGroups[indexOfAttrGroup] = attrGroup;
                    }
                    this.setState({ attributesGroups: attributesGroups });
                });
            }
        }
    }

    deleteAttribute = (attribute: IAttribute) => {
        let attributesGroups = [...this.state.attributesGroups];
        let attrGroup = attributesGroups.find(attrG => attrG.Id == attribute.AttributeGroupId);
        if (attrGroup) {
            let attrIndex = attrGroup.attributes.findIndex(x => x.Id == attribute.Id);
            if (attrIndex > -1) {

                attrGroup.attributes.splice(attrIndex, 1);
                NotificationManager.info("Deleting attribute", 'Deleting', 3000, null, null, '');
                this.attributesClient.Update(this.attributeGroupToCreateAttributeGroup(attrGroup), this.props.dataKey).then(response => {
                    if (response.data.Error) {
                        NotificationManager.error(response.data.Error, 'Update category error', 3000, null, null, '');
                        return;
                    }
                    NotificationManager.success("Attribute deleted", 'Deleted', 3000, null, null, '');
                    attrGroup = Object.assign(attrGroup, this.responseItemToAttributeGroup(response.data.Data));

                    this.setState({ attributesGroups: attributesGroups });
                });
            }
        }
    }

    onSave = (category: IAttributeGroup) => {
        if (category.Id == 0) {
            this.create(category);
        } else {
            this.edit(category);
        }
    }

    onSaveAttribute = (attribute: ICreateAttribute) => {
        if (attribute.Id == 0) {
            this.createAttribute(attribute);
        } else {
            this.editAttribute(attribute);
        }
    }

    onDelete = (type: "group" | "attribute") => {
        if (type == "group" && this.state.deleteAttributeGroup) {
            this.delete(this.state.deleteAttributeGroup);
            this.setState({ deleteAttributeGroup: null });
        } else if (type == "attribute" && this.state.deleteAttribute) {
            this.deleteAttribute(this.state.deleteAttribute);
            this.setState({ deleteAttribute: null });
        }
    }

    onAddAttribute = (attrGroup: IAttributeGroup) => {
        this.setState({ editAttribute: { AttributeGroupId: attrGroup.Id, Id: 0, PriceCorrectionPercent: 0, PriceCorrectionCurrent: 0, Description: "", Name: "", Position: attrGroup.attributes.length + 1 } })
    }

    // onReorderAttributes = (attributeGroup: IAttributeGroup, source: number, destination: number) => {
    // if (result.source && result.destination) {
    //     const items = reorder(this.state.categories, result.source.index, result.destination.index);
    //     items.forEach((item, index) => { item.Position = index + 1 });
    //     this.categoryClient.Positions(items.map(item => { return { id: item.Id, position: item.Position } })).then(response => {
    //         this.setState({ categories: items })
    //     });
    // }
    // }

    onReorderAttributes = (result: DropResult) => {
        if (result.source && result.destination) {
            let attributeGroupId = Number(result.source.droppableId);
            let attributeGroup = this.state.attributesGroups.find(x => x.Id == attributeGroupId);
            if (attributeGroup) {
                let attributes = reorder(attributeGroup.attributes, result.source.index, result.destination.index);
                attributes.forEach((item, index) => { item.Position = index + 1 });
                attributeGroup.attributes = attributes;
                this.edit(attributeGroup);
            }
        }
    }

    onReorderAttributesGroups = (result: DropResult) => {
        if (result.source && result.destination) {
            let attributes = reorder(this.state.attributesGroups, result.source.index, result.destination.index);
            attributes.forEach((item, index) => { item.Position = index + 1 });
            this.attributesClient.Positions(attributes.map(item => { return { id: item.Id, position: item.Position } })).then(response => {
                this.setState({ attributesGroups: attributes })
            });
        }
    }

    onDragEnd = (result: DropResult) => {
        if (result.type.indexOf("attributeGroup") > -1) {
            this.onReorderAttributes(result);
        } else if (result.type.indexOf("group") > -1) {
            this.onReorderAttributesGroups(result);
        }

        console.log(result);
    }

    get orderedAttributeGroups() {
        let attributes: IAttributeGroup[] = this.state.attributesGroups;
        return attributes.sort((a, b) => a.Position - b.Position);
    }

    render() {
        return (
            <>
                {this.state.loading && <div className="loading" />}
                <Row>
                    <Colxx xxs="12">
                        <div className="mb-2">
                            <div className="text-zero top-right-button-container">
                                <Button
                                    color="primary"
                                    size="lg"
                                    className="top-right-button"
                                    onClick={() => this.setState({ editedAttributeGroup: { Id: 0, Position: 0, Name: "", attributes: [], Key: this.props.dataKey } })}
                                >
                                    <i className="simple-icon-plus" /> ADD NEW ATTRIBUTE GROUP
                                </Button>
                                {'  '}
                            </div>
                            <Breadcrumb heading="Attributes" match={this.props.match} />
                        </div>
                        <Separator className="mb-5" />
                    </Colxx>
                </Row>
                <Row>
                    <Colxx xxs="12">
                        <DragDropContext onDragEnd={this.onDragEnd}>
                            <Droppable droppableId="attributeGroups" type="group">
                                {(provided, snapshot) => (
                                    <div {...provided.droppableProps} ref={provided.innerRef}>
                                        {this.orderedAttributeGroups.map((p, index) => {
                                            return (
                                                <Draggable key={"attrGroup" + p.Id} draggableId={p.Id.toString()} index={index}>
                                                    {(provided, snapshot) => (
                                                        <div ref={provided.innerRef} {...provided.draggableProps}>
                                                            <AttributeListItem groupDragHandler={provided.dragHandleProps} attributeGroup={p} onDelete={() => this.setState({ deleteAttributeGroup: p, deleteAttribute: null })} onEdit={() => this.setState({ editedAttributeGroup: p, editAttribute: null })} onCheckItem={() => { }} onEditAttribute={(attr) => this.setState({ editedAttributeGroup: null, editAttribute: attr })} onDeleteAttribute={(attr) => this.setState({ deleteAttributeGroup: null, deleteAttribute: attr })} onAddAttribute={(attrGroup) => this.onAddAttribute(attrGroup)} />
                                                        </div>)}
                                                </Draggable>)
                                        })}
                                        {provided.placeholder}
                                    </div>)}
                            </Droppable>
                        </DragDropContext>
                    </Colxx>
                </Row>
                <AddEditAttributeGroupModal attributeGroup={this.state.editedAttributeGroup} modalOpen={this.state.editedAttributeGroup != null} toggleModal={() => { this.setState({ editedAttributeGroup: null }) }} onSave={(attrGroup) => this.onSave(attrGroup)} />
                <AddEditAttributeModal attribute={this.state.editAttribute} modalOpen={this.state.editAttribute != null} toggleModal={() => { this.setState({ editAttribute: null }) }} onSave={(attr) => this.onSaveAttribute(attr)} />
                <DeleteModal isOpen={this.state.deleteAttributeGroup != null} body={`Do you really want delete attribute group ${this.state.deleteAttributeGroup?.Name}?`} header={"Deleting attribute group"} onDelete={() => this.onDelete("group")} toggle={() => this.setState({ deleteAttributeGroup: null })} />
                <DeleteModal isOpen={this.state.deleteAttribute != null} body={`Do you really want delete attribute ${this.state.deleteAttribute?.Name}?`} header={"Deleting attribute group"} onDelete={() => this.onDelete("attribute")} toggle={() => this.setState({ deleteAttribute: null })} />
            </>
        )
    }
}

export default Attributes;