import React from "react";
import { Row, Card, CardBody, CardTitle, FormGroup, Label, Input, FormText, CustomInput, Button, InputGroup, InputGroupAddon } from "reactstrap";
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import IntlMessages from "../../../helpers/IntlMessages";
import { HomeClient } from "../../../api/homeClient";
import { Formik, Form, Field } from "formik";
import { PhotoClient } from "../../../api/photoClient";
import { NotificationManager } from "../../../components/common/react-notifications";
import { PhotographerClient, UserSettingsDTO } from "../../../api/ApiClient";
import { API_BASE } from "../../../api/ApiSettings";
import { AuthService } from "../../../services/auth.service";
import { AdvertisementsList } from "./advertisement";
import { WEB_ORDERING_BASE_URL } from "../../../api/settings";

export class Settings extends React.Component<any, any> {

    homeClient: HomeClient = new HomeClient();
    photoClient: PhotoClient = new PhotoClient();

    photographerClient: PhotographerClient = new PhotographerClient(API_BASE)
    authService: AuthService = new AuthService();
    state = {
        MobileToken: "",

    }

    constructor(props) {
        super(props);

        this.homeClient.PhotographerDetails().then((response) => {
            if (response.data.Data) {
                this.setState({ MobileToken: response.data.Data.MobileToken })
            }
        });

    }

    get initialValues() {
        return {
            LabId: this.state.MobileToken || "",
            Promotion: null,
            Logo: null
        }
    }

    onSubmit = async (values, formik) => {

        let softwareSettings = await this.photoClient.SaveSoftwareSettings(values.Logo, values.Promotion, values.LabId);

        if (softwareSettings.data.Error) {
            NotificationManager.error("Saving error", softwareSettings.data.Error, 3000, null, null, '');
        }

        if (!softwareSettings.data.Error) {
            NotificationManager.success("Data saved", "Data saved correctly", 3000, null, null, '');
        }

        this.setState({ MobileToken: values.LabId })

        formik.setSubmitting(false);
    }

    render() {
        return (
            <>
                <Formik
                    initialValues={this.initialValues}
                    onSubmit={this.onSubmit}
                    enableReinitialize={true}
                >
                    {({
                        handleSubmit,
                        setFieldValue,
                        setFieldTouched,
                        handleChange,
                        handleBlur,
                        values,
                        errors,
                        touched,
                        isSubmitting,
                        submitForm
                    }) => (
                        <Form>
                            <Row>
                                <Colxx xxs="12">
                                    <Breadcrumb heading="Settings" match={this.props.match} />
                                    <Separator className="mb-5" />
                                </Colxx>
                            </Row>
                            <Row>
                                <Colxx xxs="12">
                                    <Card>
                                        <CardBody>
                                            <CardTitle>
                                                Software settings
                                            </CardTitle>
                                            <Form>
                                                <FormGroup>
                                                    <Label for="LabId">
                                                        Lab ID
                                                    </Label>
                                                    <InputGroup>
                                                        <Field className="form-control" name="LabId" type="text" id="LabId" placeholder={"LabId"} disabled={this.state.MobileToken ? "disabled" : ""} />
                                                    </InputGroup>

                                                </FormGroup>
                                                <FormGroup>
                                                    <Label for="logo">
                                                        Logo
                                                    </Label>
                                                    <InputGroup>
                                                        <CustomInput
                                                            type="file"
                                                            id="logo"
                                                            name="logo"
                                                            onChange={(event) => {
                                                                if (event != null && event.currentTarget != null && event.currentTarget.files) {
                                                                    setFieldValue("Logo", event.currentTarget.files[0]);
                                                                }
                                                            }} />
                                                        <InputGroupAddon addonType="append">Show</InputGroupAddon>
                                                    </InputGroup>
                                                    <FormText color="muted">
                                                        (maximum size 1270x1270 72ppi .jpg or .png)
                                                    </FormText>
                                                </FormGroup>

                                                <FormGroup>
                                                    <Label for="promotion">
                                                        Promotion
                                                    </Label>
                                                    <InputGroup>
                                                        <CustomInput
                                                            type="file"
                                                            id="promotion"
                                                            name="promotion"
                                                            onChange={(event) => {
                                                                if (event != null && event.currentTarget != null && event.currentTarget.files) {
                                                                    setFieldValue("Promotion", event.currentTarget.files[0]);
                                                                }
                                                            }} />
                                                        <InputGroupAddon addonType="append">Show</InputGroupAddon>
                                                    </InputGroup>
                                                    <FormText color="muted">
                                                        (maximu size 1440x2560 72ppi .jpg or .png
                                                    </FormText>
                                                </FormGroup>
                                                <Button type="submit" color="primary" className="mt-4">
                                                    Save
                                                </Button>
                                            </Form>
                                        </CardBody>
                                    </Card>
                                </Colxx>
                            </Row>
                        </Form>
                    )}
                </Formik>
                <Row className="mt-4">
                    <Colxx xxs="12">
                        <AdvertisementsList />
                    </Colxx>
                </Row>
                <Row className="mt-3">
                    <Colxx xxs="12">
                        <Card>
                            <CardBody>
                                <CardTitle>
                                    Direct link to your web-ordering
                                </CardTitle>

                                <div className="row">
                                    <div className="col-xs-12">
                                        <a href={`${WEB_ORDERING_BASE_URL}/photographer/${this.authService.getId()}`} target="_blank">{`${WEB_ORDERING_BASE_URL}/photographer/${this.authService.getId()}`}</a>
                                    </div>
                                </div>

                            </CardBody>
                        </Card>
                    </Colxx>
                </Row>
            </>
        );
    }
}

export default Settings;