import React from 'react';
import { v4 as uuidv4 } from 'uuid';
import {
    CustomInput,
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Input,
    Label,
    FormGroup,
    InputGroup,
    InputGroupAddon,
    FormText,
} from 'reactstrap';
import Switch from 'rc-switch';
import 'rc-switch/assets/index.css';
import { Formik, Form, Field } from 'formik';
import { IAddEditEventDialogViewModel, EventClient, IEvent } from '../../../api/eventsClient';
import { NotificationManager } from '../../../components/common/react-notifications';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSpinner } from '@fortawesome/free-solid-svg-icons';

export interface IEditEventProps {
    modalOpen: boolean,
    toggleModal: () => void,
    onEdited: () => void,
    event: IEvent
}

export class EditEventModal extends React.Component<IEditEventProps, any> {

    eventClient: EventClient = new EventClient();

    imageTypes = ["13x18", "Album A", "Album B", "Canvas"]

    _initialValues: IAddEditEventDialogViewModel | any = {
        TimeToDelete: 365,
        AdditionalInfo: "",
        EmailToClient: "",
        FirstFormat: "",
        FourthFormat: "",
        Id: 0,
        IsHidden: false,
        LinkForEmail: "",
        Name: "",
        NoPassKey: uuidv4(),
        Password: "",
        SecondFormat: "",
        ThirdFormat: "",
        File: null,
        IsPasswordProtected: false,
        NoPricing: false,
        FirstFormatPrice: 0,
        SecondFormatPrice: 0,
        ThirdFormatPrice: 0,
        FourthFormatPrice: 0,
    }

    constructor(props) {
        super(props);

        this.state = {
            formats: this.imageTypes,
        }
    }

    get initialValues() {
        if (this.props.event) {
            return { ...this.props.event, IsPasswordProtected: (this.props.event as any).Password ? true : false };
        }

        return this._initialValues;
    }

    removeFormat = (index: number) => {
        let formats = [...this.state.formats];
        formats.splice(index, 1);
        this.setState({ formats: formats });
    }

    directLink = (uuid: string): string => {
        if (uuid == null) {
            uuid = "";
        }
        return `${window.location.origin}/portal/direct/${uuid}`
    }

    resetUUid = () => {
        this.setState({ uuid: uuidv4() })
    }

    addFormat = () => {
        let formats = [...this.state.formats];
        formats.push("");
        this.setState({ formats: formats });
    }

    onSubmit = (values, formik) => {
        values.Id = this.props.event.Id;
        if (!values.IsPasswordProtected) {
            values.Password = undefined;
        }
        delete values.FaceBookModel;
        NotificationManager.info("Saving data...", "", 3000, null, null, '');
        this.eventClient.EditEvent(values).then((response) => {
            if (response.data.Error) {
                NotificationManager.error("Saving error", response.data.Error, 3000, null, null, '');
            } else {
                this.props.onEdited();
                this.props.toggleModal();
            }
            formik.setSubmitting(false);
        });
    }

    render() {
        return (<Formik
            initialValues={this.initialValues}
            onSubmit={this.onSubmit}
            enableReinitialize={true}
            validate={() => { }}
        >
            {({
                handleSubmit,
                setFieldValue,
                setFieldTouched,
                handleChange,
                handleBlur,
                values,
                errors,
                touched,
                isSubmitting,
                submitForm
            }) => (
                <Form><Modal
                    isOpen={this.props.modalOpen}
                    toggle={this.props.toggleModal}
                    wrapClassName="modal-right"
                    backdrop="static"
                >
                    <ModalHeader toggle={this.props.toggleModal}>
                        Edit Event
                    </ModalHeader>
                    <ModalBody>
                        <FormGroup>
                            <Label for="name">
                                Name
                            </Label>
                            <Field className="form-control" name="Name" type="text" id="Name" placeholder={"Event name"} maxLength={30} />
                            <FormText color="muted">
                                Maximum 30 letters
                            </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label for="info">
                                Info
                            </Label>
                            <Field className="form-control" name="AdditionalInfo" type="textarea" id="AdditionalInfo" placeholder={"Description"}>
                                {({ field, form, meta }) => {
                                    return <textarea rows={5} placeholder="Description" className="form-control" value={field.value} onChange={field.onChange} id="AdditionalInfo"></textarea>
                                }}
                            </Field>
                        </FormGroup>
                        <FormGroup className="mb-1">
                            <Label>
                                Photo formats
                            </Label>

                        </FormGroup>
                        <FormGroup>
                            <Label>
                                No pricing
                            </Label>
                            <Switch
                                loadingIcon={null}
                                className="custom-switch custom-switch-primary custom-switch-small"
                                checked={values.NoPricing}
                                onChange={(nopricing) => setFieldValue("NoPricing", nopricing)}
                            />
                        </FormGroup>
                        <FormGroup className="mb-1">
                            <div className="row">
                                <div className="col-8"><Field className="form-control" name="FirstFormat" type="text" id="FirstFormat" placeholder={"First Format"} /></div>
                                <div className="col-4"><Field className="form-control" name="FirstFormatPrice" type="number" id="FirstFormatPrice" placeholder={"Price"} /></div>
                            </div>
                        </FormGroup>
                        <FormGroup className="mb-1">
                            <div className="row">
                                <div className="col-8"><Field className="form-control" name="SecondFormat" type="text" id="SecondFormat" placeholder={"Second Format"} /></div>
                                <div className="col-4"><Field className="form-control" name="SecondFormatPrice" type="number" id="SecondFormatPrice" placeholder={"Price"} /></div>
                            </div>
                        </FormGroup>
                        <FormGroup className="mb-1">
                            <div className="row">
                                <div className="col-8"><Field className="form-control" name="ThirdFormat" type="text" id="ThirdFormat" placeholder={"Third Format"} /></div>
                                <div className="col-4"><Field className="form-control" name="ThirdFormatPrice" type="number" id="ThirdFormatPrice" placeholder={"Price"} /></div>
                            </div>

                        </FormGroup>
                        <FormGroup>
                            <div className="row">
                                <div className="col-8"><Field className="form-control" name="FourthFormat" type="text" id="FourthFormat" placeholder={"Fourth Format"} /></div>
                                <div className="col-4"><Field className="form-control" name="FourthFormatPrice" type="number" id="FourthFormatPrice" placeholder={"Price"} /></div>
                            </div>
                        </FormGroup>

                        <FormGroup>
                            <Label for="eventRemains">
                                Event remains for
                            </Label>
                            <Field className="form-control" name="TimeToDelete" type="number" id="TimeToDelete" />
                        </FormGroup>
                        <FormGroup>
                            <Label>
                                Is password protected?
                            </Label>
                            <Switch
                                loadingIcon={null}
                                className="custom-switch custom-switch-primary custom-switch-small"
                                checked={values.IsPasswordProtected}
                                onChange={(isPasswordProtected) => setFieldValue("IsPasswordProtected", isPasswordProtected)}
                            />
                        </FormGroup>
                        {
                            values.IsPasswordProtected && <FormGroup>
                                <Label for="password">
                                    Password
                                </Label>
                                <Field className="form-control" name="Password" type="text" id="Password" placeholder={"Password"} />
                            </FormGroup>
                        }
                        <FormGroup>
                            <Label for="file">
                                Event cover
                            </Label>
                            <InputGroup>
                                <CustomInput
                                    type="file"
                                    id="File"
                                    name="File"
                                    onChange={(event) => {
                                        if (event != null && event.currentTarget != null && event.currentTarget.files) {
                                            setFieldValue("File", event.currentTarget.files[0]);
                                        }
                                    }}
                                />
                            </InputGroup>
                            <FormText color="muted">
                                Maximum 1920x1280 pixels
                            </FormText>
                        </FormGroup>
                        <FormGroup>
                            <Label>
                                Is event hidden?
                            </Label>
                            <Switch
                                loadingIcon={null}
                                className="custom-switch custom-switch-primary custom-switch-small"
                                checked={values.IsHidden}
                                onChange={(isHidden) => setFieldValue("IsHidden", isHidden)}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="customerEmail">
                                Email to Customer
                            </Label>
                            <Field className="form-control" name="EmailToClient" type="email" id="EmailToClientName" placeholder={"ex. email@gmail.com"} />
                        </FormGroup>
                        <FormGroup>
                            <Label for="EmailLinkType">
                                Call to action
                            </Label>
                            <Field className="form-control" name="EmailLinkType" as="select" id="EmailLinkType" >
                                <option disabled hidden selected>Choose a type of the link for e-mail</option>
                                <option value={1}>Link to galery</option>
                                <option value={2}>Direct link to event</option>
                                <option value={3}>Add your Personal</option>
                            </Field>
                        </FormGroup>
                        {
                            values.EmailLinkType == 3 && <FormGroup>
                                <Label for="LinkForEmail">
                                    Personal link
                                </Label>
                                <Field className="form-control" name="LinkForEmail" type="text" id="LinkForEmail" placeholder={"Personal link"} />
                            </FormGroup>
                        }

                        <FormGroup>
                            <Label for="directLink">
                                Direct link
                            </Label>
                            <InputGroup className="mb-3">
                                <Input disabled={true} value={this.directLink(values.NoPassKey)} type="text" name="directLink" id="directLink" />
                                <InputGroupAddon addonType="append">
                                    <Button onClick={() => setFieldValue("NoPassKey", uuidv4())} outline color="secondary">
                                        RESET
                                    </Button>
                                </InputGroupAddon>
                            </InputGroup>
                        </FormGroup>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="secondary" outline onClick={this.props.toggleModal}>
                            Cancel
                        </Button>
                        <Button disabled={isSubmitting} color="primary" type="submit" onClick={() => submitForm()}>
                            {isSubmitting && <FontAwesomeIcon size="lg" icon={faSpinner} spin={true} style={{ color: "#fff" }} />} Submit
                        </Button>
                    </ModalFooter>
                </Modal>
                </Form>
            )}</Formik>
        );
    }
}

export default EditEventModal;
